/**=====================
     Product scss
==========================**/
.vertical-product-box {
  &:hover {
    .option {
      opacity: 1;
      visibility: visible;

      li {
        opacity: 1;
        transform: translateY(0);

        &:nth-child(1) {
          transition-delay: 0.1s;
        }

        &:nth-child(2) {
          transition-delay: 0.2s;
        }

        &:nth-child(3) {
          transition-delay: 0.3s;
        }

        &:nth-child(4) {
          transition-delay: 0.4s;
        }
      }
    }
  }

  .option {
    @include center(vertical);
    left: calc(11px + (23 - 11) * ((100vw - 320px) / (1920 - 320)));
    display: flex;
    align-items: center;
    position: absolute;
    gap: 8px;
    background-color: #f8f8f8;
    @include pseudowh;
    transition: all 0.5s ease-in-out;
    opacity: 0;
    visibility: hidden;

    body.rtl & {
      padding-right: 0;
    }

    @include mq-max(xs) {
      margin: 0 -1px;
    }

    li {
      @include pseudowh($width: 40px, $height: 40px);
      @include flex_common;
      background-color: $white;
      border-radius: 50%;
      border: 1px solid var(--theme-color);
      opacity: 0;
      transform: translateY(40px);
      transition: all 0.5s ease-in-out;
      position: relative;
      overflow: hidden;

      @include mq-max(xs) {
        @include pseudowh($width: 30px, $height: 30px);
        margin: 0 1px;
      }

      &:before {
        @include pos;
        @include pseudowh;
        top: 0;
        left: 0;
        background-color: var(--theme-color);
        opacity: 0.1;
        z-index: -1;
      }

      a {
        z-index: 1;
        @include flex_common;

        .icli {
          font-size: 20px;
          color: $black;

          @include mq-max(xs) {
            font-size: 15px;
          }

          &.iconly-Swap {
            transform: rotate(90deg);
          }
        }
      }
    }
  }
}

.add-to-cart-box {
  background-color: #f8f8f8;
  border-radius: 50px;
  position: relative;
  max-width: 100%;

  .btn-add-cart {
    padding: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320))) 0;
    width: 100%;
    font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
    margin-top: 10px;
    color: $content-color;
    border-radius: 50px;
    font-weight: 500;

    .add-icon {
      @include center(vertical);
      @include flex_common;
      @include pseudowh($width: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))),
        $height: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))));
      background-color: $white;
      border-radius: 100%;
      color: var(--theme-color);
      position: absolute;
      right: 4px;

      &.bg-light-orange {
        background-color: #f9f8f6;
      }

      &.bg-light-gray {
        background-color: #f8f8f8;
      }

      @include mq-max(xs) {
        display: none;
      }

      i {
        font-size: 22px;
      }
    }
  }

  .qty-box {
    @include pseudowh;
    @include center(both);
    max-width: 100%;
    position: absolute;
    margin: 0;
    display: none;

    &.open {
      display: block;
    }

    .input-group {
      background-color: $light-gray;
      border-radius: 4px;
      padding: 4px;
      text-align: center;
      z-index: 0;

      &.theme-bg-white {
        background-color: $white;
      }

      button {
        @include pseudowh($width: 32px, $height: 32px);
        @include flex_common;
        border: none;
        padding: 0;
        background-color: $white;
        z-index: 0;
        border-radius: 2px !important;

        >div {
          display: flex;
          line-height: 1;
          align-items: center;
        }

        i {
          font-size: 20px;
          color: var(--theme-color);
          margin-top: 1px;
        }

        &:focus {
          box-shadow: none;
        }

        &:hover {
          background-color: $white;

          i {
            color: var(--theme-color);
          }
        }
      }

      input {
        height: auto;
        background-color: transparent;
        border: none;
        padding: 0;
        text-align: center;
        font-size: 14px;
        color: $content-color;
      }
    }
  }
}

.add-to-cart-btn-2 {
  .btn {
    @include flex_common;
    @include pseudowh($width: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))),
      $height: calc(30px + (37 - 30) * ((100vw - 320px) / (1920 - 320))));
    position: absolute;
    right: 0;
    bottom: 0;
    background-color: #f7f7f7;
    padding: 0;
    border-radius: 100%;
    color: var(--theme-color);

    body.rtl & {
      right: unset;
      left: 0;
    }
  }

  .qty-box-2 {
    display: none;
    bottom: 0;
    position: absolute;
    left: 0;
    width: 100%;
    height: auto;
    margin-left: auto;
    max-width: 190px;

    body.rtl & {
      left: unset;
      right: 0;
    }

    &.qty-box-3 {
      max-width: 203px;
    }

    &.open {
      display: block;
    }

    .input-group {
      background-color: $light-gray;
      border-radius: 5px;
      padding: 4px;
      text-align: center;
      z-index: 0;

      button {
        @include pseudowh($width: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))),
          $height: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))));
        @include flex_common;
        border: none;
        padding: 0;
        background-color: $white;
        z-index: 0;
        border-radius: 3px !important;

        i {
          font-size: 13px;
          color: var(--theme-color);
          margin-top: 1px;
        }

        &:focus {
          box-shadow: none;
        }
      }

      input {
        height: auto;
        background-color: transparent;
        border: none;
        padding: 0;
        text-align: center;
        font-size: 14px;
        color: $content-color;
      }
    }
  }
}

.qty-box {
  margin-top: 10px;
  width: 100%;
  max-width: 170px;

  .input-group {
    background-color: $light-gray;
    border-radius: 7px;
    padding: 4px;
    text-align: center;
    z-index: 0;

    &.theme-bg-white {
      background-color: $white;
    }

    button {
      @include pseudowh($width: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))),
        $height: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))));
      @include flex_common;
      border: none;
      padding: 0;
      background-color: $white;
      z-index: 0;
      border-radius: 4px !important;

      i {
        font-size: 13px;
        color: var(--theme-color);
        margin-top: 1px;
      }

      &:focus {
        box-shadow: none;
      }
    }

    input {
      height: auto;
      background-color: transparent;
      border: none;
      padding: 0;
      text-align: center;
      font-size: 14px;
      color: $content-color;
    }
  }
}

.deal-timer {
  margin: 11px 0;

  &.product-deal-timer {
    width: 290px;
    margin-top: 24px;

    ul {
      padding: calc(8px + (14 - 8) * ((100vw - 320px) / (1920 - 320)));
      background-color: #f8f8f8;
      border-radius: 8px;
      border: none;

      li {
        +li {
          &::before {
            left: -25px;

            body.rtl & {
              left: unset;
              right: -25px;
            }
          }
        }

        .counter {
          text-align: center;

          >div {
            font-size: calc(18px + (20 - 18) * ((100vw - 320px) / (1920 - 320)));
          }
        }
      }
    }
  }

  .deal-title {
    font-weight: 600;
    margin-bottom: 7px;
    letter-spacing: 0.4px;
  }

  ul {
    @include flex_common($dis: flex, $align: center, $justify: space-between);
    padding: 8px 12px;
    border: 1px dashed var(--theme-color);
    text-align: center;

    li {
      position: relative;

      +li {
        &::before {
          @include pos($pos: absolute, $content: ":");
          @include center(vertical);
          left: -20px;

          body.rtl & {
            left: unset;
            right: -20px;
          }
        }
      }

      .counter {
        >div {
          display: flex;
          align-items: center;

          h5 {
            font-weight: 600;
            margin-bottom: 4px;
          }

          h6 {
            color: $content-color;
            font-size: 13px;
          }
        }
      }
    }
  }
}

.category-menu {
  background-color: $light-gray;
  padding: calc(12px + (33 - 12) * ((100vw - 320px) / (1920 - 320)));
  border-radius: 5px;

  &-2 {
    background: linear-gradient(136.8deg, #f6f5f2 17.01%, #fdf7f0 93.97%);
  }

  h3 {
    margin-bottom: 40px;
    font-weight: 600;
    position: relative;
    display: inline-block;

    &::before {
      @include pos;
      @include pseudowh($width: 102px, $height: 2px);
      bottom: calc(-11px + (-8 - -11) * ((100vw - 320px) / (1920 - 320)));
      left: 0;
      background-color: var(--theme-color);

      body.rtl & {
        left: unset;
        right: 0;
      }
    }
  }

  ul {
    @include flex_wrap($dis: flex, $wrap: wrap, $gap: 23px);

    &.value-list {
      margin-top: 35px;
      border: none;
    }

    li {
      display: block;
      width: 100%;

      &.pb-30 {
        margin-bottom: 30px;
      }

      .category-list {
        @include flex_wrap($dis: flex,
          $wrap: nowrap,
          $gap: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320))));
        align-items: center;
        color: $title-color;

        &:hover {
          h5 {
            a {
              letter-spacing: 0.5px;

              &:before {
                width: 70px;
              }
            }
          }
        }

        svg {
          @include pseudowh($width: 25px, $height: 25px);
          stroke: $title-color;
        }

        img {
          @include pseudowh($width: 25px, $height: 25px);
          object-fit: contain;
        }

        h5 {
          a {
            color: $title-color;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            position: relative;
            z-index: 0;
            transition: all 0.3s ease-in-out;

            &:hover {
              color: $title-color;
            }

            &::before {
              @include pos;
              @include pseudowh($width: 0, $height: 3px);
              bottom: 0;
              left: 0;
              opacity: 0.5;
              border-radius: 50px;
              background-color: var(--theme-color);
              z-index: -1;
              transition: all 0.3s ease-in-out;

              body.rtl & {
                left: unset;
                right: 0;
              }
            }
          }
        }

        i {
          margin-left: auto;
        }
      }
    }
  }

  &.menu-xl {
    position: sticky;
    top: 130px;

    ul {
      gap: 28px;
      border-bottom: none;

      li {
        .category-list {
          img {
            width: 35px;
            height: 35px;
          }

          h5 {
            font-size: 17px;
          }
        }
      }
    }
  }
}

.product-modern {
  border: 1px solid $border-color;
  border-radius: 0px;
  padding: 0;

  >.product-box-slider {
    >div {
      >.row {
        >div {
          &:nth-child(n + 6) {
            border-top: 1px solid rgba($content-color, 0.3);
          }

          &:nth-child(5n + 5) {
            .product-box {
              border-right: none;

              body.rtl & {
                border-left: none;
                border-right: unset;
              }
            }
          }

          @media screen and (max-width: 616px) {
            &:nth-child(5n + 2) {
              .product-box {
                border-right: none;

                body.rtl & {
                  border-left: none;
                  border-right: unset;
                }
              }
            }
          }
        }
      }
    }
  }
}

.product-box {
  padding: calc(8px + (14 - 8) * ((100vw - 320px) / (1920 - 320)));
  position: relative;
  overflow: hidden;
  border: 1px solid $border-color;

  body.rtl & {
    direction: rtl;
  }

  &:hover {
    .product-image {
      a {
        img {
          transform: scale(1.1);
        }
      }

      .product-option {
        bottom: 15px;
        opacity: 1;
      }
    }
  }

  &.product-box-bg {
    background: #f8f8f8;
    border-radius: 5px;
    padding: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
    border: none;

    .qty-box {
      .input-group {
        background-color: $white;
        box-shadow: 0 0 8px rgba(34, 34, 34, 0.04);
      }
    }

    +.product-box-bg {
      margin-top: 20px !important;

      @include mq-max(sm) {
        margin-top: 10px !important;
      }
    }
  }

  &.product-white-bg {
    background-color: $white;
    border-radius: calc(9px + (16 - 9) * ((100vw - 320px) / (1920 - 320)));
    padding: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
    border: 1px solid #eee;

    .qty-box {
      @include pseudowh;
      top: 0;
      right: 0;
      max-width: 60%;
      position: absolute;
      margin: 0;

      body.rtl & {
        right: unset;
        left: 0;
      }

      .input-group {
        border-radius: 100px;
        background-color: $light-gray;
        box-shadow: unset;
        border: unset;

        button {
          width: 28px;
          height: 28px;
          border-radius: 100% !important;

          >div {
            line-height: 1;
          }
        }
      }
    }

    &:hover {
      box-shadow: 0 15px 70px rgba($black, 0.07);

      html.dark & {
        box-shadow: 0 15px 70px rgba(137, 137, 137, 0.05);
      }
    }

    .add-to-cart-btn-2 {
      .addcart-button {
        position: relative;
        inset: unset;
      }
    }

    .bottom-content {
      display: flex;
      align-items: flex-end;
      justify-content: space-between;
      position: relative;
    }
  }

  .product-image {
    text-align: center;
    padding: 20px;
    position: relative;

    @include mq-max(xs) {
      padding: 8px;
    }

    .product-option {
      @include center(horizontal);
      @include flex_common;
      position: absolute;
      bottom: -5px;
      background-color: $white;
      padding: 10px 5px;
      border-radius: 7px;
      box-shadow: 0 0 8px rgba(34, 34, 34, 0.12);
      opacity: 0;
      transition: all 0.3s ease-in-out;

      @include mq-max(xs) {
        display: none;
      }

      li {
        position: relative;
        width: 40px;

        a {
          color: $title-color;

          i {
            font-size: 16px;
          }

          .feather {
            @include pseudowh($width: 17px, $height: auto);
            color: $content-color;
          }
        }

        +li {
          &:after {
            @include pos;
            @include pseudowh($width: 1px, $height: 100%);
            top: 0;
            left: 0;
            background-color: $content-color;
            opacity: 0.3;

            body.rtl & {
              left: unset;
              right: 0;
            }
          }
        }
      }
    }

    img {
      @include pseudowh($width: 100%,
        $height: calc(100px + (140 - 100) * ((100vw - 320px) / (1920 - 320))));
      object-fit: contain;
      transition: opacity 0.5s, transform 1.25s cubic-bezier(0, 0, 0.44, 1.18);
    }
  }

  .product-detail {
    .brand-name {
      color: $content-color;
      margin-bottom: 5px;
    }

    a {
      color: $title-color;
      display: block;

      &:hover {
        color: $title-color;
      }

      h6 {
        &.name {
          font-weight: 600;
          line-height: 22px;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;
          font-size: 16px;

          &-2 {
            -webkit-line-clamp: 1;
          }
        }
      }
    }

    p {
      display: none;
    }

    .byers {
      color: #999;
    }

    .product-rating {
      flex-wrap: wrap;
      gap: calc(4px + (7 - 4) * ((100vw - 320px) / (1920 - 320)));
      margin-top: 2px;

      body.rtl & {
        justify-content: flex-start;
      }

      h6 {
        font-weight: 500;
      }
    }
  }

  .progress {
    height: 6px;
    border-radius: 0;
    margin-top: calc(14px + (22 - 14) * ((100vw - 320px) / (1920 - 320)));
  }

  .stock {
    font-size: 14px;
    color: var(--theme-color);
    margin-left: auto;
  }

  .sold {
    font-weight: 600;
    margin-top: 3px;
    line-height: 21px;
    font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));

    body.rtl & {
      text-align: right;
    }

    .price {
      font-weight: 600;
      margin-top: 0;
    }

    del {
      font-weight: 400;
      margin-left: 6px;
      font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));

      body.rtl & {
        margin-left: unset;
        margin-right: 6px;
      }
    }

    &.weight {
      font-size: 13px;
      margin: calc(5px + (8 - 5) * ((100vw - 320px) / (1920 - 320))) 0;
      line-height: normal;
    }
  }

  .counter-box {
    display: block;
    position: relative;

    h6 {
      margin-top: 0;
    }

    .addtocart_btn {

      .add-button,
      .add_cart {
        @include flex_common;
        position: absolute;
        bottom: 0;
        right: 0;
        padding: 7px 11px;
        background-color: var(--theme-color);
        color: white;
        font-size: 16px;
        font-weight: 600;
        transition: all 0.3s ease;

        span {
          position: relative;
          left: unset;
          right: unset;
          bottom: unset;
          font-size: 15px;
          margin-right: 8px;

          @include mq-max(xs) {
            display: none;
          }
        }
      }

      .qty-box {
        display: none;
        transition: all 0.3s ease;
        z-index: 1;
        margin-top: 0;
        max-width: 155px;

        &.open {
          display: block;
          position: absolute;
          right: 0;
          bottom: 0;
          width: 100%;
          transition: all 0.3s ease;
        }
      }

      .input-group {
        box-shadow: 0 2px 4px 0 #dedede;
        border-radius: 2px;
        padding: 4px;
        text-align: center;
        z-index: 0;
        background-color: $white;

        .btn {
          padding: 7px 12px;
          font-size: 10px;
          line-height: 1;
          border-radius: 2px !important;
          background-color: #f8f8f8;

          i {
            font-size: 15px;
          }
        }

        .form-control {
          padding: 2px;
          text-align: center;
          border-color: #f9f9f9;
        }
      }
    }
  }

  .price {
    margin-top: 16px;
    font-weight: 600;
  }

  .add-to-cart-box {
    .qty-box {
      .input-group {
        border-radius: 100px;
      }

      .input-group {
        button {
          border-radius: 100% !important;
        }
      }
    }
  }
}

