.product-style-2 {
  padding: 6px 20px;
  overflow: hidden;
  position: relative;
  border-right: 1px solid rgba(74, 85, 104, 0.1);

  &:hover {
    .product-image {
      a {
        img {
          transform: scale(1.1);
        }
      }

      .product-option {
        bottom: 15px;
        opacity: 1;
      }
