      }

      li {
        position: relative;
        width: 33.33%;

        a {
          color: $title-color;

          .feather {
            @include pseudowh($width: 17px, $height: auto);
            color: $content-color;
          }
        }

        +li {
          &:after {
            @include pos;
            @include pseudowh($width: 1px, $height: 100%);
            top: 0;
            left: 0;
            background-color: $content-color;
            opacity: 0.3;

            body.rtl & {
              left: unset;
              right: 0;
            }
          }
        }
      }
    }

    img {
      @include pseudowh($width: 100%, $height: 140px);
      object-fit: contain;
      transition: all 0.3s ease-in-out;
    }
