.border-effect {
  border: 1px solid $border-color;
  border-radius: 5px;
  position: absolute;
  right: 0;
  top: 0;
  width: calc(33.33% - 20px);
  height: 100%;
  z-index: -1;

  @media (max-width: 1400px) and (min-width: 1200px) {
    display: none;
  }

  @media (max-width: 830px) {
    display: none;
  }
}

.product-list {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 50px calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
  padding: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320))) 0;

  @media (max-width: 1400px) and (min-width: 1200px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 830px) {
    grid-template-columns: 1fr 1fr;
  }

  @media (max-width: 576px) {
    grid-template-columns: 1fr;
    gap: 30px calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
  }

  &.sidebar-product {
    gap: 50px calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));

    li {
      padding: 0;

      +li {
        &:after {
          @include pos;
          top: -28px;
          right: 0;
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - 103px);

          body.rtl & {
            right: unset;
            left: 0;
          }
        }
      }
    }
  }

  &:after,
  &:before {
    @include pos;
    border: 1px solid $border-color;
    border-radius: 10px;
    left: 0;
    top: 0;
    width: calc(33.33% - 12px);
    height: 100%;
    z-index: -1;

    @media (max-width: 1400px) and (min-width: 1200px) {
      width: calc(50% - 12px);
    }

    @media (max-width: 830px) {
      width: calc(50% - 15px);
    }

    @media (max-width: 576px) {
      width: 100%;
    }
  }

  &:before {
    left: calc(33.33% + 10px);

    @media (max-width: 1400px) and (min-width: 1200px) {
      left: calc(50% + 10px);
    }

    @media (max-width: 830px) {
      left: calc(50% + 15px);
      width: calc(50% - 15px);
    }

    @media (max-width: 576px) {
      left: 0;
      width: 100%;
    }
  }

  li {
    width: 100%;
    display: flex;
    align-items: center;
    height: auto;
    position: relative;
    padding: 0 calc(18px + (30 - 18) * ((100vw - 1199px) / (1920 - 1199)));

    @media (min-width: 1400px) {
      &:nth-child(n + 4) {
        &:after {
          @include pos;
          top: calc(-22px + (-28 - -22) * ((100vw - 992px) / (1920 - 992)));
          right: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - calc(120px + (143 - 120) * ((100vw - 320px) / (1920 - 320))) - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320))));

          body.rtl & {
            left: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            right: unset;
          }
        }
      }
    }

    @media (max-width: 1400px) and (min-width: 1200px) {
      &:nth-child(n + 3) {
        &:after {
          @include pos;
          top: calc(-22px + (-28 - -22) * ((100vw - 992px) / (1920 - 992)));
          right: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - calc(120px + (143 - 120) * ((100vw - 320px) / (1920 - 320))) - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320))));

          body.rtl & {
            left: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            right: unset;
          }
        }
      }
    }

    @media (max-width: 1200px) and (min-width: 830px) {
      &:nth-child(n + 4) {
        &:after {
          @include pos;
          top: calc(-22px + (-28 - -22) * ((100vw - 992px) / (1920 - 992)));
          right: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - calc(120px + (143 - 120) * ((100vw - 320px) / (1920 - 320))) - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320))));

          body.rtl & {
            left: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            right: unset;
          }
        }
      }
    }

    @media (max-width: 830px) and (min-width: 576px) {
      &:nth-child(n + 3) {
        &:after {
          @include pos;
          top: -28px;
          right: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - calc(120px + (143 - 120) * ((100vw - 320px) / (1920 - 320))));

          body.rtl & {
            left: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            right: unset;
          }
        }
      }
    }

    @media (max-width: 576px) {
      &:nth-child(n + 2) {
        &:after {
          @include pos;
          top: -18px;
          right: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
          border-bottom: 1px dashed rgba(74, 85, 104, 0.3);
          width: calc(100% - calc(120px + (143 - 120) * ((100vw - 320px) / (1920 - 320))));

          body.rtl & {
            left: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            right: unset;
          }
        }
      }
    }

    .offer-product {
      @include pseudowh;
      display: flex;
      align-items: center;
      gap: calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)));

      &:hover {
        .offer-image {
          transform: scale(1.1);
        }

        .offer-detail {
          a {
            .name {
              color: var(--theme-color);
            }
          }
        }
      }

      .offer-image {
        width: calc(75px + (80 - 75) * ((100vw - 320px) / (1920 - 320)));

        @include mq-max(4xl) {
          width: calc(80px + (90 - 80) * ((100vw - 320px) / (1920 - 320)));
        }

        img {
          @include pseudowh($width: 80px, $height: 80px);
          object-fit: contain;
          transition: all 0.3s ease-in-out;
        }
      }

      .offer-detail {
        @include pseudowh($width: calc(100% - calc(75px + (80 - 75) * ((100vw - 320px) / (1920 - 320))) - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320)))),
          $height: 80px);
        position: relative;
        display: flex;
        align-items: center;

        @include mq-max(4xl) {
          width: calc(100% - calc(80px + (90 - 80) * ((100vw - 320px) / (1920 - 320))) - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320))));
        }

        .vertical-price {
          display: flex;
          align-items: center;
          gap: 4px;
          margin-top: calc(2px + (6 - 2) * ((100vw - 320px) / (1920 - 320)));
          white-space: nowrap;
          margin-bottom: -2px;

          .price {
            font-weight: 600;
          }
        }

        a {
          display: block;
          color: $title-color;

          &:hover {
            color: $title-color;
          }

          .name {
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            font-weight: 500;
            margin-top: -1px;
            transition: all 0.3s ease-in-out;
            font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
          }
        }

        span {
          font-size: 14px;
          margin-top: 5px;
          color: $content-color;
          font-weight: 400;
        }
      }
    }
  }
}

.product-category {
  padding: calc(19px + (36 - 19) * ((100vw - 320px) / (1920 - 320))) calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
  background-color: $light-gray;

  .product-category-list {
    column-count: 2;

    body.rtl & {
      padding-left: unset;
      padding-right: 0;
    }

    li {
      display: block;
      padding-left: 22px;
      font-size: 14px;
      color: $content-color;
      position: relative;
      width: 140px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;

      body.rtl & {
        padding-left: unset;
        padding-right: 22px;
      }

      &::before {
        @include pos;
        @include pseudowh($width: 7px, $height: 7px);
        top: 50%;
        transform: rotate(45deg) translateY(-50%);
        left: 0;
        border: 1px solid $content-color;

        body.rtl & {
          left: unset;
          right: 8px;
        }
      }

      +li {
        margin-top: calc(14px + (26 - 14) * ((100vw - 320px) / (1920 - 320)));
      }

      &:last-child {
        a {
          display: flex;
          align-items: center;

          &:hover {
            color: var(--theme-color);
          }

          i {
            margin-left: 8px;
          }
        }

        &::before {
          display: none;
        }
      }
    }
  }
}

