  position: relative;
  overflow: hidden;
  background-color: $white;

  html.dark & {
    border-color: rgba(119, 119, 119, 0.44);
  }

  &:hover {
    .label-tag {
      left: 0;

      span {
        width: 100%;
      }
    }
  }

  .label-tag {
    position: absolute;
    top: calc(13px + 11*(100vw - 320px) / 1600);
    left: -5px;
    padding: calc(4px + 1*(100vw - 320px) / 1600) calc(5px + 3*(100vw - 320px) / 1600);
    font-size: calc(13px + 1*(100vw - 320px) / 1600);
    color: #ffffff;
    font-weight: 500;
    display: flex;
    align-items: center;
    transition: all 0.1s 0.05s cubic-bezier(0.86, 0, 0.07, 1);
    z-index: 1;
    background-color: var(--theme-color);
    background-position: 204px;

    body.rtl & {
      direction: ltr;
    }

    &::after,
    &::before {
      content: "";
      position: absolute;
      width: 15px;
      height: 15px;
      right: -15px;
      border-bottom: 15px solid var(--theme-color);
      border-left: 15px solid transparent;
      border-right: 15px solid transparent;
      transform: rotate(0);
      z-index: 1;
    }

    &::before {
      top: 0;
      transform: rotate(180deg);
    }

    &::after {
      bottom: 0;
      transform: rotate(0);
    }

    i {
      margin-left: 5px;
    }

    span {
      overflow: hidden;
      transition: width 0.1s 0.05s cubic-bezier(0.86, 0, 0.07, 1);
      width: 0;
    }
  }

  .img-box {
    position: relative;

    a {
      border-radius: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));
      overflow: hidden;
      display: block;
      text-align: center;

      img {
        object-fit: cover;
        object-position: left top;
        height: calc(164px + (192 - 164) * ((100vw - 320px) / (1920 - 320)));
      }
    }

    .heart-icon {
      position: absolute;
      right: calc(5px + (10 - 5) * ((100vw - 320px) / (1920 - 320)));
      top: calc(5px + (10 - 5) * ((100vw - 320px) / (1920 - 320)));
