      height: calc(28px + (35 - 28) * ((100vw - 320px) / (1920 - 320)));
      @include flex_common;
      background-color: rgba($black, 0.40);
      border-radius: 100%;

      html.dark & {
        color: #ffffff;
      }

      svg {
        width: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
        height: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
      }
    }
  }

  .content-box {
    margin-top: 10px;

    .top-content {
      h5 {
        font-weight: 600;
        color: $title-color;
        margin-bottom: 4px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }

      h6 {
        color: #999;
        margin-bottom: 4px;
        line-height: 1.2;

