    .list-style {
      .product-style-2 {
        padding: 0;

        .product-image {
          margin: 0;
          width: calc(180px + (250 - 180) * ((100vw - 575px) / (1920 - 575)));

          img {
            height: auto;
          }
        }

        .product-detail {
          padding: 0;
          width: calc(100% - calc(180px + (250 - 180) * ((100vw - 575px) / (1920 - 575))) - 20px);
        }
      }

      @media (max-width: 575px) {
        .product-box {
          .product-detail {
            padding: calc(10px + 4 * (100vw - 320px) / 1600);
            padding-top: 0;
            margin-inline: unset;
          }
        }

        .product-theme-box {
          .content-box {
            margin: 10px;
            margin-top: 20px;
          }
        }

        .product-style-1 {
          .product-detail {
            padding: calc(10px + 4 * (100vw - 320px) / 1600);
            padding-top: 0;

            p {
              -webkit-line-clamp: 2;
            }
          }
        }

        .product-style-2 {
          .product-image {
            margin: 0;
            width: 100%;

            img {
              height: calc(160px + 40 * (100vw - 320px) / 1600);
            }
          }

          .product-detail {
            padding: calc(10px + 4 * (100vw - 320px) / 1600);
            padding-top: 0;
            width: 100%;

            p {
              margin-bottom: 8px;
              -webkit-line-clamp: 2;
            }
          }
        }
      }
    }
  }

  &.row {
    >.col-6 {
      .product-box {
        border: unset;
      }
    }
  }

  .list-style {

    .product-box.product-white-bg,
    .product-theme-box {
      padding: 0;

      .product-image,
      .img-box {
        padding: 20px;
        margin: 0;
        width: calc(200px + (230 - 200) * ((100vw - 575px) / (1920 - 575)));

        @include mq-max(sm) {
          width: 100%;
        }
      }

      .product-detail,
      .content-box {
        width: calc(100% - calc(200px + (230 - 200) * ((100vw - 575px) / (1920 - 575))) - 40px);
        padding: 0;
        margin: 0;
        margin-inline: 20px;

        @include mq-max(sm) {
          width: auto;
          margin-bottom: 20px;
        }
      }
    }

    .product-box,
    .product-style-1 {
      padding: 0;

      .product-image {
        margin: 0;
        width: calc(180px + (250 - 180) * ((100vw - 576px) / (1920 - 576)));

        @include mq-max(sm) {
          width: 100%;
        }

        img {
          height: auto;

          @include mq-max(sm) {
            height: calc(160px + 40*(100vw - 320px) / 1600);
          }

        }
      }

      .product-detail {
        padding-bottom: 0;
        padding-left: 0;
        width: calc(100% - calc(180px + (250 - 180) * ((100vw - 576px) / (1920 - 576))) - 20px);

        body.rtl & {
          padding-left: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
          padding-right: unset;
        }

        @include mq-max(sm) {
          width: auto;
        }
      }
    }

    .product-style-2 {
      padding: 0;

      .product-image {
        width: calc(180px + (250 - 180)*((100vw - 576px) /(1920 - 576)));
        margin-bottom: 0;

        @include mq-max(sm) {
          width: 100%;
        }

        img {
          height: auto;

          @include mq-max(sm) {
            height: calc(160px + 40*(100vw - 320px) / 1600);
          }
        }
      }

      .product-detail {
        width: calc(100% - calc(180px + (250 - 180)*((100vw - 576px) /(1920 - 576))) - 20px);

        @include mq-max(sm) {
          width: 100%;
        }
      }
    }

    .product-theme-box {
      .content-box {
        @include mq-max(sm) {
          margin: 0;
          margin-top: 20px;
        }
      }
    }
  }
}

.releted-product {
  .full_border {
    >.row {
      margin: 0;

      >div {
        padding: 0;
        margin: 0;
        border-top: unset;
      }

      .col-xxl-2 {
        @media screen and (min-width: 1399px) {
          border-right: 1px solid $border-color;

          body.rtl & {
            border-right: unset;
            border-left: 1px solid $border-color;

            html.dark & {
              border-color: rgba(119, 119, 119, 0.44);
            }
          }

          &:nth-child(6n+6) {
            border-right: unset;

            body.rtl & {
              border-right: unset;
              border-left: unset;
            }
          }
        }
      }

      .col-lg-3 {
        @media (max-width: 1400px) and (min-width: 991px) {
          border-right: 1px solid $border-color;

          body.rtl & {
            border-right: unset;
            border-left: 1px solid $border-color;

            html.dark & {
              border-color: rgba(119, 119, 119, 0.44);
            }
          }

          &:nth-child(4n+4) {
            border-right: unset;

            body.rtl & {
              border-right: unset;
              border-left: unset;
            }
          }
        }
      }

      .col-md-4 {
        @media (max-width: 992px) and (min-width: 767px) {
          border-right: 1px solid $border-color;

          body.rtl & {
            border-right: unset;
            border-left: 1px solid $border-color;

            html.dark & {
              border-color: rgba(119, 119, 119, 0.44);
            }
          }

          &:nth-child(3n+3) {
            border-right: unset;

            body.rtl & {
              border-right: unset;
              border-left: unset;
            }
          }
        }
      }
    }
  }
}

.not-available {
  margin-top: 13px;
  font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
  color: #de1a2e;
  letter-spacing: 0.3px;
}