/**=====================
     Inner scss
==========================**/
.table {
  &:not(:last-child) {
    &:last-child {
      * {
        border-bottom: none;
      }
    }
  }
}

.inner-title {
  margin-bottom: calc(32px + (55 - 32) * ((100vw - 320px) / (1920 - 320)));
  text-align: center;

  h2 {
    font-size: calc(21px + (35 - 21) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 700;
    color: $title-color;
    padding-bottom: 20px;
    display: inline-block;
    text-transform: none;
    position: relative;

    &::after {
      @include pos;
      @include center(horizontal);
      @include pseudowh($width: 70px, $height: 1px);
      bottom: 0;
      background-color: var(--theme-color);
    }

    img {
      @include center(horizontal);
      bottom: -11px;
      position: absolute;
      background-color: $white;
      z-index: 1;
      padding: 0 10px;
    }
  }

  &-2 {
    margin-bottom: calc(18px + (38 - 18) * ((100vw - 320px) / (1920 - 320)));

    h3 {
      font-size: calc(24px + (30 - 24) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 600;
    }
  }
}

/**=====================
     404 Scss
==========================**/
.section-404 {
  .image-404 {
    text-align: center;

    img {
      width: 25%;

      @include mq-max(3xl) {
        width: 40%;
      }

      @include mq-max(lg) {
        width: 50%;
      }

      @include mq-max(sm) {
        width: 70%;
      }
    }
  }

  .contain-404 {
    width: 50%;
    text-align: center;
    margin: calc(20px + (45 - 20) * ((100vw - 320px) / (1920 - 320))) auto 0;
    font-family: $public-sans;

    @include mq-max(4xl) {
      width: 60%;
    }

    @include mq-max(xl) {
      width: 80%;
    }

    @include mq-max(md) {
      width: 100%;
    }

    h3 {
      font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.5;
      font-weight: 400;
    }

    button {
      margin-top: calc(16px + (24 - 16) * ((100vw - 320px) / (1920 - 320)));
    }
  }
}

/**=====================
     Cart Scss
==========================**/
.cart-section {
  .deliver-box {
    padding: 22px 18px;
    background-color: $light-gray;
    border-radius: 6px;
    position: relative;
    margin-bottom: calc(22px + (40 - 22) * ((100vw - 320px) / (1920 - 320)));

    .btn-close {
      @include center(vertical);
      position: absolute;
      right: 18px;
    }

    h5 {
      &:first-child {
        margin-bottom: 8px;
      }

      &:last-child {
        font-weight: 500;
        line-height: 1.4;

        a {
          text-decoration: underline;
          margin-left: 5px;
        }
      }
    }
  }

  .cart-title {
    margin-bottom: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
  }

  .promo-code-box {
    margin-bottom: calc(18px + (22 - 18) * ((100vw - 320px) / (1920 - 320)));

    .form-label {
      font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
    }
  }

  .total-box {
    @include flex_common($dis: flex, $align: center, $justify: space-between);
    padding: 22px 28px;
    border: 1px solid $border-color;
  }

  .member-button {
    font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 500;
    letter-spacing: 1.4px;
  }

  .special-seller-box {
    padding: 20px;
    background-color: $light-gray;
    border-radius: 6px;
    border-radius: 5px;
    border: 1px dashed #bfbfbf;

    h5 {
      font-weight: 500;
      font-size: 20px;
      letter-spacing: 0;
      padding-bottom: 18px;
      border-bottom: 1px solid $content-color;
      margin-bottom: 25px;
    }

    .sub-total-table {
      tbody {
        tr {
          td {
            &:last-child {
              font-weight: 500;
            }
          }

          &.sub-total {
            td {
              font-size: 16px;
              font-weight: 400;
              padding-bottom: 26px;

              &:last-child {
                text-align: right;
              }
            }
          }

          &.shipping-title {
            td {
              border: none;
              padding-top: 20px;
              padding-bottom: 10px;
            }
          }

          &.shipping-list {
            td {
              border: none;

              .form-check {
                margin-bottom: 0;

                .form-check-input {
                  @include pseudowh($width: 18px, $height: 18px);
                  margin-top: 2px;
                  margin-right: 10px;

                  &:checked {
                    background-color: var(--theme-color);
                    border-color: var(--theme-color);
                  }

                  &:focus {
                    box-shadow: none;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .cart-total-box {
    padding: 20px;
    background-color: $light-gray;
    border-radius: 6px;
    text-align: center;

    h3 {
      font-weight: bold;
      color: $title-color;
      margin-bottom: 15px;
    }

    h6 {
      width: 100%;
      margin: 20px 0 10px;
      color: $title-color;
      font-weight: 500;
      font-size: 16px;
    }

    p {
      margin: 0;
      font-size: 14px;
      letter-spacing: 0.5px;
      line-height: 1.4;
    }

    .checkout-button {
      font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
      margin-top: 12px;
    }
  }
}

/**=====================
     Checkout Scss
==========================**/
/* Shopping Cart Box */
.cart-table {
  background-color: $light-gray;
  padding: calc(18px + (35 - 18) * ((100vw - 320px) / (1920 - 320))) calc(12px + (25 - 12) * ((100vw - 320px) / (1920 - 320)));

  &.cart-table-2 {
    background-color: transparent;
    padding: 0;

    table {
      margin-bottom: 0;

      thead {
        tr {
          th {
            font-size: 20px;
            font-weight: 500;
            color: $content-color;
            padding: 0 22px 16px;
          }
        }
      }

      tbody {
        border-top: 1px solid $border-color;

        tr {
          td {
            vertical-align: middle;
          }

          &:first-child {
            td {
              padding: 22px 16px;
            }
          }
        }
      }
    }
  }

  table {
    margin-bottom: 0;

    tbody {
      tr {
        &:last-child {
          td {
            border: none;
            padding-bottom: 0;
          }
        }

        &:first-child {
          td {
            padding-top: 0;
          }
        }

        td {
          padding: calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320))) 16px;
          min-width: calc(135px + (170 - 135) * ((100vw - 320px) / (1920 - 320)));

          .table-title {
            margin-bottom: 12px;
            font-size: 14px;
            font-weight: 500;
          }

          &.product-detail {
            max-width: 260px;

            &:hover {
              .product {
                .product-image {
                  img {
                    transform: scale(1.1);
                  }
                }
              }
            }

            ul {
              li {
                +li {
                  margin-bottom: 2px;
                }
              }
            }

            .product {
              display: flex;
              align-items: center;
              overflow: hidden;

              @include mq-max(2xl) {
                align-items: flex-start;
              }

              .product-image {
                width: 70px;

                @include mq-max(2xl) {
                  width: 80px;
                }

                @include mq-max(sm) {
                  width: 90px;
                }

                @include mq-max(xs) {
                  width: 100px;
                }

                @include mq-max(2xs) {
                  width: 70px;
                }

                img {
                  transition: all 0.3s ease-in-out;
                }
              }

              .product-detail {
                margin-left: calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
                margin-top: 0;
                width: calc(100% - 95px);

                body.rtl & {
                  margin-left: unset;
                  margin-right: calc(16px + (25 - 16) * ((100vw - 320px) / (1920 - 320)));
                }

                ul {
                  body.rtl & {
                    padding-right: 0;
                  }

                  li {
                    display: block;
                    white-space: nowrap;
                    margin-bottom: 8px;
                    font-size: 14px;

                    +li {
                      margin-bottom: calc(0px + (2 - 0) * ((100vw - 320px) / (1920 - 320)));
                    }

                    .saving {
                      font-weight: 500;
                    }

                    span {
                      font-weight: 500;
                    }

                    &.name {
                      a {
                        font-weight: 500;
                        font-size: 16px;
                        width: 100%;
                        overflow: hidden;
                        text-overflow: ellipsis;
                        display: block;
                        color: $title-color;

                        &:hover {
                          color: $title-color;
                        }
                      }
                    }

                    &:nth-child(n + 4) {
                      display: none;
                    }

                    .price {
                      display: flex;
                      align-items: center;

                      h6 {
                        margin-left: 8px;
                        text-decoration: line-through;
                      }
                    }

                    &.quantity-price-box {
                      border: 1px solid $border-color;
                      border-radius: 5px;
                      width: 62%;

                      @include mq-max(sm) {
                        width: 80%;
                      }

                      @include mq-max(2xs) {
                        width: 90%;
                      }

                      .cart_qty {
                        button {
                          padding: calc(3px + (10 - 3) * ((100vw - 320px) / (1920 - 320))) calc(10px + (18 - 10) * ((100vw - 320px) / (1920 - 320)));
                          border: none;

                          input {
                            padding: 10px 18px;
                            border: none;
                            text-align: center;
                          }
                        }
                      }
                    }

                    &.quantity {
                      margin: 6px 0;

                      .quantity-price {
                        width: 50%;

                        @include mq-max(xs) {
                          width: 60%;
                        }

                        .cart_qty {
                          button {
                            @include pseudowh($width: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))),
                              $height: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))));
                            @include flex_common;
                            border: none;
                            background-color: #e3e0e0;
                            border-radius: 100% !important;
                            padding: 0;

                            i {
                              margin-top: 2px;
                            }
                          }

                          input {
                            padding: 0;
                            border: none;
                            text-align: center;
                            background-color: transparent;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          &.price {
            h5 {
              font-weight: 500;
              font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
              display: flex;
              align-items: center;

              del {
                font-size: calc(13px + (16 - 13) * ((100vw - 320px) / (1920 - 320)));
                margin-left: 4px;

                body.rtl & {
                  margin-left: unset;
                  margin-right: 4px;
                }
              }
            }

            h6 {
              margin-top: 5px;
              white-space: nowrap;
              font-size: calc(15px + (16 - 15) * ((100vw - 320px) / (1920 - 320)));
              font-weight: 500;
            }
          }

          &.saving {
            font-size: 16px;
            font-weight: 500;
            color: var(--theme-color);
          }

          &.quantity {
            width: 20%;
          }

          .quantity-price {
            width: 117px;

            .cart_qty {
              button {
                @include pseudowh($width: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))),
                  $height: calc(29px + (35 - 29) * ((100vw - 320px) / (1920 - 320))));
                @include flex_common;
                border: none;
                background-color: #e3e0e0;
                border-radius: 100% !important;
                padding: 0;
                z-index: 0;

                i {
                  font-size: 16px;
                }
              }

              input {
                padding: 0;
                border: none;
                text-align: center;
                background-color: transparent;
              }
            }
          }

          &.subtotal {
            h5 {
              font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
              font-weight: 500;
              display: inline-block;
            }

            .btn-close {
              margin-left: 50px;
            }
          }

          &.save-remove {
            .save {
              font-weight: 400;
              white-space: nowrap;
              font-size: 16px;
              display: block;
              text-decoration: underline;

              &:hover {
                color: var(--theme-color);
              }
            }

            .remove {
              font-weight: 400;
              font-size: 15px;
              color: #bf2020;
              text-decoration: underline;
            }
          }
        }
      }
    }

    tfoot {
      border-top: 1px solid $border-color;

      tr {
        td {
          &:first-child {
            border: none;
          }

          &:nth-child(2) {
            font-size: 16px;
            font-weight: 500;
            padding: 16px;
            border-left: 1px solid $border-color;
          }

          &:last-child {
            font-size: 16px;
            font-weight: 500;
            border-right: 1px solid $border-color;
          }
        }
      }
    }
  }
}

/* Summery cart box */
.summery-box {
  border-radius: 5px;
  background-color: $light-gray;

  .summery-header {
    padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
    display: flex;
    align-items: center;
    border-bottom: 1px solid $border-color;

    h3 {
      font-weight: 600;
    }

    a {
      font-weight: 500;
      margin-left: auto;
      font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));

      &:hover {
        color: var(--theme-color);
      }

      body.rtl & {
        margin-left: unset;
        margin-right: auto;
      }
    }
  }

  .summery-contain {
    padding: 16px calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 1px solid $border-color;

    &::-webkit-scrollbar-track {
      background-color: $content-color;
    }

    &::-webkit-scrollbar {
      width: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--theme-color);
      border-radius: 4px;
    }

    .coupon-cart {
      .coupon-box {
        position: relative;
        overflow: hidden;
        border-radius: 4px;

        .btn-apply {
          background: var(--theme-color);
          color: #ffffff;
          padding: 0 calc(16px + (30 - 16) * ((100vw - 320px) / (1920 - 320)));
          font-weight: 700;
          border: none;
        }

        .form-control {
          border: 2px solid var(--theme-color);
          padding: 10px;
        }
      }
    }

    ul {
      body.rtl & {
        padding-right: 0;
      }

      li {
        padding: 10px 0;
        display: flex;
        align-items: center;

        &:last-child {
          border-bottom: none;
        }

        h4 {
          font-size: 15px;
          color: $content-color;

          &.price {
            color: $content-color;
            margin-left: auto;

            body.rtl & {
              margin-left: unset;
              margin-right: auto;
            }

            .shpping-contain {
              display: block;
              font-weight: 400;
              font-size: 13px;
              margin-top: 3px;
            }
          }

          span {
            font-weight: 600;
          }
        }
      }
    }
  }

  .summery-contain {
    padding: calc(11px + (16 - 11) * ((100vw - 320px) / (1920 - 320))) calc(11px + (22 - 11) * ((100vw - 320px) / (1920 - 320)));
    border-bottom: 1px solid $border-color;

    &::-webkit-scrollbar-track {
      background-color: transparent;
    }

    &::-webkit-scrollbar {
      width: 3px;
    }

    &::-webkit-scrollbar-thumb {
      background-color: var(--theme-color);
      border-radius: 4px;
    }

    li {
      padding: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320))) 0;
      display: flex;
      align-items: center;

      &:last-child {
        border-bottom: none;
      }

      p {
        font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
        line-height: 1.6;
        margin: 0;
      }

      h4 {
        font-size: 15px;
        color: $content-color;

        &.price {
          color: $content-color;
          margin-left: auto;

          body.rtl & {
            margin-left: unset;
            margin-right: auto;
          }

          .shpping-contain {
            display: block;
            font-weight: 400;
            font-size: 13px;
            margin-top: 3px;
          }
        }

        span {
          font-weight: 600;
        }
      }
    }
  }

  .summery-total {
    padding: 0 calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));

    li {
      padding-top: 12px;
      display: flex;
      align-items: center;

      h4 {
        font-size: 17px;
        color: $title-color;

        &.price {
          margin-left: auto;

          body.rtl & {
            margin-left: unset;
            margin-right: auto;
          }
        }

        span {
          font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
          font-weight: 600;
        }
      }

      &:nth-child(4) {
        padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) 0;

        h4 {
          font-weight: 600;
          color: var(--theme-color);
        }
      }

      &:last-child {
        border-top: 1px solid $border-color;
        padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) 0;

        h4 {
          font-weight: 600;
          font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
        }
      }
    }
  }
}

.button-group {
  text-align: right;
  margin-top: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));

  &.cart-button {
    margin-top: 0;
    padding: 0 calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320))) calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));

    ul {
      body.rtl & {
        padding-right: 0;
      }

      li {
        width: 100%;
      }
    }
  }

  ul {
    @include flex_common($dis: flex, $align: center, $justify: flex-end);
    flex-wrap: wrap;
    gap: calc(7px + (16 - 7) * ((100vw - 320px) / (1920 - 320)));

    body.rtl & {
      padding-right: 0;
    }

    &.button-group-list {
      @include flex_common($dis: flex, $align: center, $justify: space-between);
      margin-top: calc(26px + (45 - 26) * ((100vw - 320px) / (1920 - 320)));
    }

    li {
      a {
        width: 100%;
        font: inherit;
        letter-spacing: 0.04em;
        padding: calc(8px + (12 - 8) * ((100vw - 320px) / (1920 - 320))) calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));

        &.shopping-button {
          font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));
          background-color: $border-color;
          color: $black;

          i {
            margin-right: 8px;

            body.rtl & {
              margin-right: unset;
              margin-left: 8px !important;
            }
          }
        }
      }
    }
  }
}

.checkout-section {
  .custom-accordion {
    @include flex_wrap($dis: flex, $wrap: wrap, $gap: calc(10px + (30 - 10) * ((100vw - 320px) / (1920 - 320))));
    align-items: center;

    .accordion-item {
      border: none;
      border-radius: 6px;
      overflow: hidden;
      display: block;
      width: 100%;

      .accordion-header {
        .accordion-button {
          color: $content-color;
          position: relative;
          padding: 0;

          &::after {
            content: none;
          }

          &::before {
            @include pos($pos: absolute, $content: "\f107");
            @include font;
            @include center(vertical);
            right: 30px;
            transition: transform 0.2s ease-in-out;

            body.rtl & {
              right: unset;
              left: 30px;
            }
          }

          &:not(.collapsed) {
            color: $title-color;
            background-color: transparent;
            box-shadow: none;

            &::before {
              top: 20%;
              transform: rotate(-180deg) translateY(-50%);
            }
          }

          .form-check {
            display: flex;
            align-items: center;
            width: 100%;

            .form-check-label {
              font-weight: 500;
              color: $title-color;
              display: flex;
              align-items: center;
              width: 100%;
              font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
              padding: 16px 20px;

              body.rtl & {
                padding-right: 0;
              }

              @include mq-max(xs) {
                margin-top: 5px;
              }

              .form-check-input {
                margin-right: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));

                body.rtl & {
                  margin-left: calc(6px + (10 - 6) * ((100vw - 320px) / (1920 - 320)));
                  margin-right: unset;
                }
              }
            }
          }
        }
      }

      .accordion-collapse {
        .accordion-body {
          .cod-review {
            a {
              &:hover {
                color: var(--theme-color);
              }
            }
          }

          .custom-form-check {
            margin-bottom: calc(5px + (8 - 5) * ((100vw - 320px) / (1920 - 320)));
            display: flex;
            align-items: center;

            body.rtl & {
              padding-left: unset;
              padding-right: 0;
            }

            label {
              display: flex;
              align-items: center;
              font-size: calc(14px + (17 - 14) * ((100vw - 320px) / (1920 - 320)));
            }

            input {
              margin-right: 10px;

              body.rtl & {
                margin-right: unset;
                margin-left: 10px;
              }
            }
          }
        }
      }
    }
  }

  .custom-navtab {
    @include flex_wrap($dis: flex, $wrap: wrap, $gap: calc(0px + (24 - 0) * ((100vw - 320px) / (1920 - 320))));
    overflow: auto;
    position: sticky;
    top: 0;

    @include mq-max(lg) {
      display: flex;
      flex-wrap: nowrap;
    }

    .nav-item {
      width: 100%;
      flex-basis: auto;

      @include mq-max(lg) {
        display: inline-block;

        +.nav-item {
          margin-left: 15px;
        }
      }

      .nav-link {
        position: relative;
        text-align: left;
        border: 2px solid $border-color;
        padding: calc(10px + (18 - 10) * ((100vw - 320px) / (1920 - 320)));
        cursor: pointer;

        @include mq-max(lg) {
          width: calc(195px + (220 - 195) * ((100vw - 320px) / (1920 - 320)));
        }

        &::before {
          @include pos;
          @include pseudowh($width: 0px, $height: 100%);
          bottom: 0;
          left: 0;
          background-color: var(--theme-color);
          background-color: transparent;
          transition: 0.3s ease;
          border-radius: 4px;
        }

        &.active {
          color: $title-color;
          background-color: transparent;
          border: 2px solid var(--theme-color);

          &::before {
            box-shadow: 0 4px 0px var(--theme-color);
            width: 100%;
          }

          .nav-item-box {

            h4,
            i {
              color: var(--theme-color);
              z-index: 1;
              -webkit-text-stroke: 1px transparent;
            }
          }
        }

        .nav-item-box {
          position: relative;
          display: flex;
          align-items: center;

          span {
            color: $content-color;
            font-size: 12px;
            font-weight: 500;
            margin-bottom: calc(0px + (5 - 0) * ((100vw - 320px) / (1920 - 320)));
          }

          h4 {
            color: $title-color;
            font-weight: 500;
            font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
          }

          .lord-icon {
            @include pseudowh($width: calc(38px + (58 - 38) * ((100vw - 320px) / (1920 - 320))), $height: auto);
            margin-left: auto;

            body.rtl & {
              margin-left: unset;
              margin-right: auto;
            }
          }
        }
      }
    }
  }

  .tab-content {
    background-color: $light-gray;
    height: 100%;
    padding: calc(26px + (35 - 26) * ((100vw - 320px) / (1920 - 320))) calc(12px + (25 - 12) * ((100vw - 320px) / (1920 - 320)));

    .tab-pane {
      .tab-title {
        margin-bottom: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
        font-size: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
        color: $title-color;
      }
    }
  }

  /* Delivery Address */
  .delivery-address-box {
    border-radius: 8px;
    padding: calc(12px + (24 - 12) * ((100vw - 320px) / (1920 - 320)));
    background-color: $white;
    box-shadow: 0 0 9px rgba($black, 0.07);

    >div {
      display: flex;
      position: relative;

      .label {
        position: absolute;
        top: 0;
        right: 0;
        background-color: var(--theme-color);
        padding: 2px 8px;
        border-radius: 4px;
        color: #ffffff;
        font-size: 12px;
        letter-spacing: 0.8px;

        body.rtl & {
          right: unset;
          left: 0;
        }
      }

      .form-check {
        .form-check-input {
          @include pseudowh($width: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320))),
            $height: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320))));
          margin-top: 2px;

          &:checked {
            background-color: var(--theme-color);
            border-color: var(--theme-color);
          }

          &:checked[type="radio"] {
            background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='-4 -4 8 8'%3e%3ccircle r='2' fill='%23fff'/%3e%3c/svg%3e");
          }

          &:focus {
            box-shadow: none;
          }
        }
      }

      .delivery-address-detail {
        @include flex_wrap($dis: flex, $wrap: wrap, $gap: 10px);
        margin-left: 10px;
        width: calc(85% + (75 - 85) * ((100vw - 320px) / (1920 - 320)));

        body.rtl & {
          margin-left: unset;
          margin-right: 10px;
          padding-right: 0;
        }

        li {
          display: block;
          width: 100%;
        }

        p,
        h6 {
          line-height: 1.4;
          margin-bottom: 0;
          font-weight: 400;
          font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));

          span {
            margin-right: 4px;
          }
        }
      }
    }
  }

  .add-address {
    @include flex_common;
    padding: 30px;
    height: 100%;
    text-align: center;
    cursor: pointer;
    background-color: $white;
    box-shadow: 0 0 9px rgba($black, 0.07);
    border-radius: 8px;
  }

  /* Delivery Option */
  .delivery-option {
    padding: calc(17px + (26 - 17) * ((100vw - 320px) / (1920 - 320)));
    background-color: $white;
    border-radius: 5px;

    .select-option {
      @include mq-max(3xl) {
        margin-top: 6px;
      }
    }

    .date-box {
      position: relative;

      i {
        @include center(vertical);
        position: absolute;
        right: 17px;
        font-size: 18px;
        color: $content-color;
      }
    }

    .delivery-category {
      display: flex;
      align-items: center;
      height: 100%;

      @include mq-max(md) {
        display: block;
      }
    }

    .custom-form-check {
      display: flex;
      align-items: center;
      margin-bottom: 0;
      min-height: auto;

      body.rtl & {
        padding-left: unset;
        padding-right: 1.5rem;
      }

      .form-check-label {
        font-size: calc(15px + (19 - 15) * ((100vw - 320px) / (1920 - 320)));
        padding-left: 12px;
        font-weight: 500;

        body.rtl & {
          padding-left: unset;
          padding-right: 12px;
        }
      }
    }

    .delivery-items {
      display: flex;
      align-items: center;
      height: 100%;

      @include mq-max(sm) {
        display: flex;
        align-items: center;
      }

      @include mq-max(xs) {
        display: block;
      }

      h5 {
        letter-spacing: 0.5px;
        display: flex;
        align-items: center;

        &.items {
          margin-bottom: 5px;

          @include mq-max(sm) {
            margin-bottom: 0;
          }

          @include mq-max(xs) {
            margin-bottom: 5px;
          }

          span {
            text-decoration: underline;
          }
        }

        &.charge {
          @include mq-max(sm) {
            margin-top: 8px;
          }
        }

        i {
          font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
          margin-left: 7px;
          color: rgba($black, 0.25);

          body.rtl & {
            margin-left: unset;
            margin-right: 7px;
          }
        }
      }
    }

    .delivery-date {
      font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
      border: 1px solid $border-color;
      background-color: $white;
      display: flex;
      align-items: center;
      color: $content-color;
      width: 100%;
      padding: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320))) calc(15px + (25 - 15) * ((100vw - 320px) / (1920 - 320)));

      input {
        font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 500;
        border: none;
        background-color: $white;
        display: flex;
        align-items: center;
        color: $content-color;
        width: 100%;
      }
    }

    .delivery-time {
      display: flex;
      align-items: center;
      height: 100%;

      .dropdown-toggle {
        font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 500;
        border: 1px solid $border-color;
        background-color: $white;
        display: flex;
        align-items: center;
        color: $content-color;

        i {
          -webkit-text-stroke: 1px $content-color;
          color: transparent;
          font-size: 18px;
          margin-right: 10px;
        }

        &::after {
          content: none;
        }

        &::before {
          @include pos($pos: absolute, $content: "\f107");
          @include font;
          @include center(vertical);
          right: 20px;
        }
      }

      .dropdown-menu {
        border: 1px solid transparent;
        box-shadow: 0 6px 5px rgba($black, 0.1);

        li {
          a {
            &:active {
              color: $black;
              text-decoration: none;
              background-color: $border-color;
            }
          }

          +li {
            margin-top: 5px;
          }
        }
      }
    }
  }

  .payment-button {
    padding: calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320))) calc(15px + (25 - 15) * ((100vw - 320px) / (1920 - 320)));
    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
    font-weight: 500;
  }

  /* Payment Method */
  .payment-method {
    .custom-form-check {
      label {
        font-weight: 500;
        font-size: 17px;
      }
    }
  }

  .credit-card-box {
    .credit-detail {
      position: relative;
    }
  }

  .credit-info {
    label {
      font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
    }
  }

  .payment-option {
    padding: 20px;
    box-shadow: 0 0 5px rgba($black, 0.17);
    border-radius: 8px;
    position: relative;

    .form-check {
      .form-check-input {
        margin-top: 4px;
      }

      label {
        font-weight: 500;
        font-size: 18px;
        padding-left: 10px;
      }
    }

    img {
      @include center(vertical);
      position: absolute;
      right: 20px;
      width: 60px;
    }
  }
}

/**=====================
     Contact Us Scss
==========================**/
.contact-box-section {
  .left-sidebar-box {
    .contact-image {
      text-align: center;
      margin-bottom: calc(25px + (64 - 25) * ((100vw - 320px) / (1920 - 320)));

      @include mq-max(xl) {
        margin-bottom: 0;
        @include flex_common;
        @include pseudowh;
      }

      @include mq-max(lg) {
        margin-bottom: calc(25px + (64 - 25) * ((100vw - 320px) / (1920 - 320)));
      }

      img {
        width: 60%;

        @include mq-max(xl) {
          width: 77%;
        }

        @include mq-max(lg) {
          width: 60%;
          margin-bottom: calc(25px + (64 - 25) * ((100vw - 320px) / (1920 - 320)));
