          body.rtl & {
            left: unset;
            right: 0;
          }
        }
      }
    }

    .contact-detail {
      .contact-detail-box {
        @include flex_wrap($dis: flex, $wrap: wrap, $gap: calc(2px + (5 - 2) * ((100vw - 320px) / (1920 - 320))));
        align-items: center;
        background-color: $light-gray;
        padding: calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320))) calc(30px + (41 - 30) * ((100vw - 320px) / (1920 - 320)));
        position: relative;
        border-radius: 10px;
        margin-left: 22px;

        body.rtl & {
          padding: calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320))) calc(30px + (41 - 30) * ((100vw - 320px) / (1920 - 320))) calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (27 - 15) * ((100vw - 320px) / (1920 - 320)));
          margin-left: 0;
          margin-right: 22px;
        }

        .contact-icon {
          @include center(vertical);
          position: absolute;
          left: -21px;
          background-color: var(--theme-color);
          border-radius: 7px;
          display: inherit;
          color: #ffffff;
          width: 42px;
          height: 42px;
          @include flex_common;

          body.rtl & {
            left: unset;
            right: -21px;
          }

          i {
            line-height: 1;
            font-size: 18px;
          }
        }

        .contact-detail-title,
        .contact-detail-contain {
          width: 100%;
        }

        .contact-detail-title {
          h4 {
            font-weight: 600;
            font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
            margin-top: -2px;
          }
        }

        .contact-detail-contain {
          p {
            margin: 0;
            font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
            line-height: 1.5;
            color: $content-color;
            margin-bottom: -5px;
          }
        }
      }
    }
  }

  .right-sidebar-box {
    padding: calc(21px + (60 - 21) * ((100vw - 320px) / (1920 - 320)));
    border-radius: 10px;
    background-color: $light-gray;

    .custom-form {
      label {
        color: $content-color;
        display: block;
        font-size: 16px;
        margin-bottom: calc(3px + (8 - 3) * ((100vw - 320px) / (1920 - 320)));
      }

      .custom-input {
        position: relative;

        .form-control {
          padding: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) calc(41px + (52 - 41) * ((100vw - 320px) / (1920 - 320)));
          border: none;

          body.rtl & {
            padding: calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) calc(41px + (52 - 41) * ((100vw - 320px) / (1920 - 320))) calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320))) calc(15px + (20 - 15) * ((100vw - 320px) / (1920 - 320)));
          }

          &:focus {
            background-color: $white;
            border-color: transparent;
          }
        }

        i {
          position: absolute;
          top: 22px;
          left: 0;
          padding: 0 calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
          display: flex;
          align-items: center;
          font-size: calc(14px + (18 - 14) * ((100vw - 320px) / (1920 - 320)));
          color: $content-color;
          line-height: 1;

          body.rtl & {
            left: unset;
            right: 0;
          }
        }
      }

      .custom-textarea {
        position: relative;

        .form-control {
          padding-left: 52px;
          border: none;

          body.rtl & {
            padding-left: unset;
            padding-right: 52px;
          }

          &:focus {
            background-color: $white;
            border-color: transparent;
          }
        }

        i {
          position: absolute;
          top: 0;
          left: 0;
          height: 100%;
          padding: 18px 20px;
          line-height: 1;
          display: flex;
          font-size: 18px;
          color: $content-color;

          body.rtl & {
            left: unset;
            right: 0;
          }
        }
      }
    }
  }
}

.map-section {
  .map-box {
    margin-bottom: -6px;
    display: block;

    iframe {
      @include pseudowh($width: 100%, $height: 350px);
    }
  }
}

/**=====================
     Log In Us Scss
==========================**/
.log-in-section {
  overflow-x: hidden;
  display: flex;
  align-items: center;
  position: relative;
  z-index: 0;

  &::after {
    @include pos;
    @include pseudowh;
    top: 0;
    left: 0;
    background-image: url("../../images/inner-page/log-in-bg.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    z-index: -1;
  }

  &.otp-section {
    display: flex;
    align-items: center;
  }

  .image-contain {
    @include flex_common;
    height: 100%;
  }

  .log-in-form {
    .form-control {
      border: none;
    }

    .select2-container {
      .select2-selection {
        .select2-selection__rendered {
          background-color: transparent !important;
        }
      }
    }
  }

  .inputs {
    margin: calc(-2px + (-8 - 2) * ((100vw - 320px) / (1920 - 320)));

    input {
      margin: calc(2px + (8 - 2) * ((100vw - 320px) / (1920 - 320)));
      border: none;
    }
  }

  .log-in-box {
    background-color: $light-gray;
    padding: calc(16px + (50 - 16) * ((100vw - 320px) / (1920 - 320)));
    border-radius: calc(8px + (12 - 8) * ((100vw - 320px) / (1920 - 320)));
    width: 100%;

    .logo-name {
      margin-bottom: calc(20px + (30 - 20) * ((100vw - 320px) / (1920 - 320)));
      margin-top: -10px;

      a {
        font-size: calc(28px + (35 - 28) * ((100vw - 320px) / (1920 - 320)));
        font-weight: bold;
        color: $title-color;
        font-family: $public-sans;
        display: block;

        img {
          width: calc(150px + (225 - 150) * ((100vw - 320px) / (1920 - 320)));
        }
      }
    }

    .log-in-title {
      margin-bottom: calc(12px + (20 - 12) * ((100vw - 320px) / (1920 - 320)));

      h3 {
        margin-bottom: calc(1px + (6 - 1) * ((100vw - 320px) / (1920 - 320)));
        font-size: calc(18px + (21 - 18) * ((100vw - 320px) / (1920 - 320)));
      }

      h4,
      h5 {
        color: $content-color;
        margin-top: 8px;
      }
    }

    .log-in-button {
      margin-top: 20px;
      padding: 1px 0;

      ul {
        @include flex_wrap($dis: flex, $wrap: wrap, $gap: 15px);
        align-items: center;

        body.rtl & {
          padding-right: 0;
        }

        li {
          display: block;
          width: 100%;

          a {
            @include flex_common;
            font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
            color: $title-color;
            border-radius: 5px;
            padding: calc(10px + (16 - 10) * ((100vw - 320px) / (1920 - 320))) calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
            flex-wrap: wrap;
            gap: calc(8px + (10 - 8) * ((100vw - 320px) / (1920 - 320)));
            background-color: $white;

            img {
              width: calc(18px + (24 - 18) * ((100vw - 320px) / (1920 - 320)));
            }
          }
        }
      }
    }

    .forgot-box {
      @include flex_common($dis: flex, $align: center, $justify: space-between);

      @include mq-max(2xs) {
        display: block;
      }
    }

    .remember-box {
      display: flex;
      align-items: center;

      .check-box {
        display: block;
        margin-top: -6px;
      }

      label {
        font-size: 16px;

        span {
          font-weight: 500;
          color: var(--theme-color);
        }
      }
    }

    .forgot-password {
      font-size: 16px;
      font-weight: 500;
      display: block;

      &:hover {
        color: var(--theme-color);
      }
    }

    .other-log-in {
      margin-top: 15px;
      text-align: center;
      position: relative;

      &::before {
        @include pos;
        @include center(vertical);
        @include pseudowh($width: 100%, $height: 1px);
        left: 0;
        background-color: #ddd;
        z-index: 0;
      }

      h6 {
        color: $content-color;
        position: relative;
        background-color: #f9f9f9;
        padding: 0 14px;
        display: inline-block;
        text-transform: uppercase;
      }
    }

    .sign-up-box {
      margin-top: 20px;
      text-align: center;

      h4 {
        color: $content-color;
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
        margin-bottom: calc(0px + (3 - 0) * ((100vw - 320px) / (1920 - 320)));
      }

      a {
        font-weight: 500;
        font-size: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320)));
        margin-bottom: -4px;
        display: block;

        &:hover {
          color: var(--theme-color);
        }
      }
    }

    .contact-title {
      margin-bottom: 30px;

      h2 {
        margin-bottom: 15px;
      }

      h5 {
        width: 53%;
        font-size: 18px;
        line-height: 1.3;
        color: $content-color;
      }
    }

    .phone-field {
      .form-floating>label {
        left: 105px;
        width: calc(100% - 105px);

        body.rtl & {
          left: unset;
          right: 105px;
        }
      }

      .form-control {
        padding-left: 123px !important;

        body.rtl & {
          padding-left: unset !important;
          padding-right: 123px !important;
        }
      }

      .custom-select {
        &.intl-tel-input {
          top: 1px;
          left: 0;
          // width: 104px;
          border: none;
          border-radius: 6px 0 0 6px;
          border-right: 1px solid $border-color;

          body.rtl & {
            left: unset;
            right: 0;
            border-radius: 0 6px 6px 0;
            border-right: unset;
            border-left: 1px solid $border-color;
          }
        }

        .select2-container {
          min-width: 110px;
          .select2-selection {
            border-radius: 6px 0 0 6px;
            border: unset !important;
            min-width: unset !important;
            width: unset !important;
            padding: 14px 12px !important;
            background-color: $white !important;

            .country {
              background-color: $white !important;
            }

            body.rtl & {
              border: unset !important;
              border-radius: 0 6px 6px 0;
            }
          }

          .select2-container-dropdown {
            &.select2-container--open {
              .select2-dropdown {
                box-shadow: unset;
              }
            }
          }
        }
      }
    }

    .number-btn{
      border: 1px solid rgba(26, 163, 135, 0.451);
      width: 100%;
      display: block;
      text-align: center;
      margin-top: 16px;
      padding: calc(10px + (14 - 10) * ((100vw - 320px) / (1920 - 320)));
      font-size: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320)));
      border-radius: calc(3px + (6 - 3) * ((100vw - 320px) / (1920 - 320)));
      background-color: rgba(26, 163, 135, 0.03);
    }
  }
}

/**=====================
     FAQ Scss
==========================**/
.faq-contain {
  margin-bottom: calc(20px + (50 - 20) * ((100vw - 320px) / (1920 - 320)));

  .faq-top-box {
    text-align: center;
    background-color: $light-gray;
    border-radius: 9px;
    padding: 28px 18px;

    .faq-box-icon {
      @include pseudowh($width: 65px, $height: 65px);
      @include flex_common;
      background-color: $white;
      border-radius: 7px;
      margin: 0 auto 16px;

      img {
        @include pseudowh($width: calc(36px + (45 - 36) * ((100vw - 320px) / (1920 - 320))), $height: auto);
      }
    }

    .faq-box-contain {
      h3 {
        font-weight: 700;
      }

      p {
        margin: 17px 0 0;
        line-height: 1.6;
        color: $content-color;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
        margin-bottom: -4px;
      }
    }
  }
}

.faq-box-contain {
  position: relative;
  z-index: 0;

  .faq-contain {
    margin-bottom: 0;
    position: sticky;
    top: 92px;

    @include mq-max(xl) {
      margin-bottom: calc(30px + (50 - 30) * ((100vw - 320px) / (1920 - 320)));
    }

    h2 {
      font-weight: 700;
      font-size: calc(28px + (56 - 28) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.4;
    }

    p {
      margin: calc(8px + (15 - 8) * ((100vw - 320px) / (1920 - 320))) 0 0;
      font-size: calc(13px + (15 - 13) * ((100vw - 320px) / (1920 - 320)));
      line-height: 1.5;
      color: $content-color;

      @include mq-max(xl) {
        width: 70%;
      }

      @include mq-max(lg) {
        width: 90%;
      }

      @include mq-max(md) {
        width: 100%;
        text-align: justify;
      }
    }
  }

  .faq-accordion {
    .accordion {
      @include flex_wrap($dis: flex, $wrap: wrap, $gap: calc(15px + (24 - 15) * ((100vw - 320px) / (1920 - 320))));

      .accordion-item {
        width: 100%;
        border-radius: 7px;
        background-color: $light-gray;
        border: none;
        overflow: hidden;

        .accordion-header {
          .accordion-button {
            background-color: $light-gray;
            font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
            font-weight: 500;
            line-height: 1.5;
            padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) calc(12px + (20 - 12) * ((100vw - 320px) / (1920 - 320)));

            i {
              margin-left: auto;
              transition: all 0.3s ease-in-out;

              body.rtl & {
                margin-left: unset;
                margin-right: auto;
              }
            }

            &::after {
              content: unset;
            }

            &:not(.collapsed) {
              font-weight: 700;
              color: var(--theme-color);
              background-color: transparent;
              box-shadow: none;

              i {
                transform: rotate(180deg);
              }
            }

            &:focus {
              border-color: transparent;
              box-shadow: none;
            }
          }
        }

        .accordion-collapse {
          .accordion-body {
            padding: 0 calc(12px + (20 - 12) * ((100vw - 320px) / (1920 - 320))) 13px;

            .accordion-body {
              padding: 0;
            }

            p {
              font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));
              line-height: 1.6;
              color: $content-color;
              margin-bottom: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320)));

              &:last-child {
                margin-bottom: -4px;
              }
            }
          }
        }
      }
    }
  }
}

/**=====================
     Compare Scss
==========================**/
.compare-section {
  .compare-table {
    border: 1px solid #ddd;
    margin-bottom: 0;

    tr {

      td,
      th {
        min-width: 200px;
        border-right: 1px solid #ddd;
        padding: calc(12px + (16 - 12) * ((100vw - 320px) / (1920 - 320))) calc(14px + (20 - 14) * ((100vw - 320px) / (1920 - 320)));
        text-align: center;

        &:first-child {
          min-width: auto;
        }
      }

      th {
        font-size: 16px;
        font-weight: 500;
        color: var(--theme-color);
        background-color: $light-gray;
      }

      td {
        font-size: 15px;

        &.title {
          a {
            font-weight: 700;
            color: #222;
          }
        }

        a {
          font-weight: 600;

          &:hover {
            color: $title-color;
          }
        }

        .compare-image {
          @include pseudowh($width: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320))),
            $height: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320))));
          @include flex_common;
          text-align: center;
          border-radius: 5px;
          background-color: #f1f1f1;
          margin: 0 auto;

          img {
            @include pseudowh($width: 100%, $height: calc(65px + (90 - 65) * ((100vw - 320px) / (1920 - 320))));
            object-fit: contain;
          }
        }

        .price {
          h5 {
            font-weight: 600;
            color: $title-color;

            del {
              font-weight: 400;
              font-size: 15px;
              margin-left: 4px;
              color: #777;
            }

            span {
              margin-left: 4px;
              color: var(--theme-color);
            }
          }
        }

        .compare-rating {
          @include flex_common;
          flex-wrap: wrap;
          gap: 5px;

          span {
            font-size: calc(14px + (15 - 14) * ((100vw - 320px) / (1920 - 320)));

            body.rtl & {
              margin-left: unset;
              padding-right: 5px;
            }
          }
        }

        .summary {
          color: #777;
          line-height: 1.5;
          margin-bottom: 0;
          font-size: 15px;
        }
      }
    }
  }

  .compare-part {
    position: relative;

    .detail-part {
      .title-detail {
        background-color: #f7f8fa;
        text-align: center;
        padding: 14px 0;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
      }

      .inner-detail {
        padding: 15px;
        text-align: center;

        p {
          margin-bottom: 0;
          line-height: 1.2;
          letter-spacing: 0.05em;
        }
      }
    }

    .img-section {
      padding-top: 20px;

      img {
        width: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320)));
        height: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320)));
        @include flex_common;
        text-align: center;
        border-radius: 5px;
        margin: 0 auto;
        object-fit: contain;
      }

      h5 {
        text-align: center;
        margin: 12px 0;
        font-size: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320)));
        text-transform: capitalize;
        font-weight: 500;
      }
    }

    .compare-rating {
      span {
        color: #fea348;
        font-size: 18px;
      }

      .rating-text {
        font-size: 16px;
        color: $content-color;
        margin-left: 6px;
      }
    }

    .btn-part {
      padding: 15px;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;

      .btn {
        margin: 0 auto;
        width: max-content;
      }
    }

    .remove-part {
      padding: 15px;
      text-align: center;
    }
  }

  .compare-row {
    border: 1px solid #eee;

    html.dark & {
      border-color: rgba(119, 119, 119, 0.44);
    }

    >div {
      +div {
        .compare-part {
          border-left: 1px solid #eee;

          html.dark & {
            border-color: rgba(119, 119, 119, 0.44);
          }

          body[class="rtl"] & {
            border-right: 1px solid #eee;
            border-left: unset;

            html.dark & {
              border-color: rgba(119, 119, 119, 0.44);
            }
          }
        }
      }
    }
  }
}

.compare-fix {
  a {
    position: fixed;
    right: calc(54px + (72 - 54) * ((100vw - 320px) / (1920 - 320)));
    background-color: var(--theme-color);
    color: #ffffff;
    padding: calc(7px + (12 - 7) * ((100vw - 320px) / (1920 - 320))) calc(10px + (12 - 10) * ((100vw - 320px) / (1920 - 320))) calc(6px + (12 - 6) * ((100vw - 320px) / (1920 - 320)));
    bottom: calc(10px + (25 - 10) * ((100vw - 320px) / (1920 - 320)));
    border-radius: 6px;
    z-index: 1;
    transition: all 0.3s ease-in-out;

    @include mq-max(md) {
      bottom: calc(75px + (76 - 75) * ((100vw - 320px) / (767 - 320)));
    }

    body.rtl & {
      left: calc(54px + (72 - 54) * ((100vw - 320px) / (1920 - 320)));
      right: unset;
    }
  }

  h5 {
    font-size: 14px;
    text-transform: uppercase;
  }
}

.skeleton-compare {
  .compare-part {
    .img-section {
      text-align: center;

      .compare-img {
        background: #f6f7f8;
        border-radius: 7px;
        width: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320)));
        height: calc(105px + (130 - 105) * ((100vw - 320px) / (1920 - 320)));
        border-radius: 5px;
        margin: 0 auto;
      }

      h5 {
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  .placeholder {
    background: #f6f7f8;
    background-position: -500px 0;
    animation: skeletonShine 1s linear 0s infinite normal forwards;
    background-image: linear-gradient(135deg, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
    background-repeat: no-repeat;
    background-size: 1000px 100%;
    border-radius: 7px;
    opacity: 1;
  }
}

/**=====================
     blog Scss
==========================**/
.blog-section {
  .left-sidebar-box {
    position: sticky;
    top: 20px;

    .left-search-box {
      .search-box {
        position: relative;

        &::before {
          @include pos($pos: absolute, $content: "\f002");
          @include font;
          @include center(vertical);
          right: 20px;
          color: $content-color;
        }

        &::after {
          @include pos;
          @include center(vertical);
          @include pseudowh($width: 1px, $height: 60%);
          right: 51px;
          opacity: 0.3;
          background-color: $content-color;
        }

        input {
          padding: 8px 68px 8px 20px;
          background-color: $light-gray;
          border: none;
          height: 53px;
          border-radius: 7px;

          &:focus {
            border-color: #ced4da;
          }
        }
      }
    }

    .left-accordion-box {
      .accordion-item {
        border: none;
        padding: calc(18px + (25 - 18) * ((100vw - 320px) / (1920 - 320)));
        background-color: $light-gray;
        border-radius: 8px;

        &.skeleton-accordion {
          .accordion-collapse {
            background-color: transparent;
          }

          .accordion-header {
            .accordion-button {
              background-color: transparent;

              span {
                background: #f6f7f8;
                background-position: -500px 0;
                animation: skeletonShine 1s linear 0s infinite normal forwards;
                background-image: linear-gradient(135deg, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
                background-repeat: no-repeat;
                background-size: 1000px 100%;
                width: 92px;
                height: 18px;
                border-radius: 9px;

                &:before {
                  display: none;
                }
              }

              &::after {
                display: none;
              }
            }
          }

          .placeholder {
            background: #f6f7f8;
            background-position: -500px 0;
            animation: skeletonShine 1s linear 0s infinite normal forwards;
            background-image: linear-gradient(135deg, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
            background-repeat: no-repeat;
            background-size: 1000px 100%;
            border-radius: 9px;
            opacity: 1;
            height: 20px;
            position: relative;
            margin-left: 30px;

            body.rtl & {
              margin-left: unset;
              margin-right: 30px;
            }

            html.dark & {
              background-color: #1d1d22;
              background-image: linear-gradient(90deg, rgba(255, 255, 255, 0), rgb(44 44 44 / 50%), rgba(255, 255, 255, 0));
            }

            &::after {
              @include pos;
              left: 0;
              width: 20px;
              height: 100%;
              left: -30px;
              background-color: #f6f7f8;
              background-position: -500px 0;
              animation: skeletonShine 1s linear 0s infinite normal forwards;
              background-image: linear-gradient(135deg, #f6f7f8 0%, #edeef1 20%, #f6f7f8 40%, #f6f7f8 100%);
              background-repeat: no-repeat;
              background-size: 1000px 100%;
              top: auto;
              border-radius: 5px;

              body.rtl & {
                left: unset;
                right: -30px;
              }
            }
          }

          .accordion-body {
            ul {
              display: flex;
              flex-direction: column;
              gap: 18px;
            }
          }
        }

        +.accordion-item {
          margin-top: 20px;
        }

        .accordion-header {
          .accordion-button {
            background-color: $light-gray;
            color: $title-color;
            font-weight: 700;
            padding: 0;

            &::after {
              @include pseudowh($width: unset, $height: unset);
              @include remixicon;
              background-image: none;
              content: "\ea4e";

              body.rtl & {
                margin-left: unset;
                margin-right: auto;
              }
            }

            &:focus {
              border-color: transparent;
              box-shadow: none;
            }

            &:not(.collapsed) {
              box-shadow: none;
            }
          }
        }

        .accordion-collapse {
          background-color: $light-gray;

          .accordion-body {
            padding: 0;
            margin-top: 20px;

            .recent-post-box {
              .recent-box {
                display: flex;
                align-items: center;

                +.recent-box {
                  margin-top: 20px;
                }

                &:hover {
                  .recent-image {
                    img {
                      transform: scale(1.1) rotate(4deg);
                    }
                  }
                }

                .recent-image {
                  width: 110px;
                  border-radius: 5px;
                  overflow: hidden;

                  img {
                    @include pseudowh;
                    transition: all 0.3s ease-in-out;
                  }
                }

                .recent-detail {
                  padding-left: 15px;
                  width: calc(100% - 110px);

                  body.rtl & {
                    padding-left: unset;
                    padding-right: 15px;
                  }

                  a {
                    color: $title-color;

                    &:hover {
                      color: $title-color;
                    }

                    h5 {
                      font-weight: 600;
                      line-height: 1.45;
                      -webkit-line-clamp: 2;
                      -webkit-box-orient: vertical;
                      display: -webkit-box;
                      overflow: hidden;
                    }
                  }

                  h6 {
                    @include flex_common($dis: flex, $align: center, $justify: space-between);
                    margin-top: 8px;
                    color: $content-color;

                    .feather {
                      @include pseudowh($width: 18px, $height: 18px);
                      stroke-width: 1;
                    }
                  }
                }
              }
            }

            .category-list-box {
              ul {
                @include flex_wrap($dis: flex, $wrap: wrap, $gap: 14px);

                body.rtl & {
                  padding-left: unset;
                  padding-right: 0;
                }

                li {
                  display: block;
                  position: relative;
                  transition: all 0.3s ease-in-out;
                  z-index: 0;
                  overflow: hidden;
                  width: 100%;

                  &:first-child {
                    padding-top: 0;
                  }

                  a {
                    display: block;
                    font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
                    color: $title-color;

                    .category-name {
                      @include flex_common($dis: flex, $align: center, $justify: space-between);

                      h5 {
                        color: $content-color;
                        font-weight: 400;
                      }

                      h5,
                      span {
                        transition: all 0.3s ease-in-out;
                      }

                      span {
                        @include pseudowh($width: 22px, $height: 22px);
                        @include flex_common;
                        font-size: 15px;
                        padding: 4px 5px 3px;
                        border-radius: 100%;
                        color: #787878;
                      }
                    }
                  }
                }
              }
            }

            .product-tags-box {
              ul {
                @include flex_wrap($dis: flex, $wrap: wrap, $gap: 10px);

                body.rtl & {
                  padding-left: unset;
                  padding-right: 0;
                }

                li {
                  z-index: 0;
                  padding: 4px 9px;
                  border-radius: 3px;
                  transition: all 0.3s ease-in-out;
                  overflow: hidden;
                  position: relative;
                  background-color: $white;

                  &::after {
                    @include pos;
                    @include pseudowh;
                    transition: all 0.3s ease-in-out;
                    z-index: -1;
                    top: 0;
                    left: 0;
                    background-color: var(--theme-color);
                    opacity: 0;
                  }

                  &:hover {
                    border-color: transparent;

                    &::after {
                      opacity: 1;
                    }

                    a {
                      color: $white;
                    }
                  }

                  a {
                    font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));
                    color: $content-color;
                    text-transform: capitalize;
                  }
                }
              }
            }

            .product-list-2 {
              li {
                +li {
                  margin-top: 30px;

                  &::after {
                    top: -16px;
                    width: calc(100% - 80px - calc(12px + (23 - 12) * ((100vw - 320px) / (1920 - 320))));
                  }
                }

                .offer-product {
                  .offer-detail {
                    a {
                      color: $title-color;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .custom-border {
    border-bottom: 1px solid $border-color;
    padding-bottom: calc(20px + (50 - 20) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: calc(35px + (60 - 35) * ((100vw - 320px) / (1920 - 320)));
  }

  .blog-box {
    height: 100%;

    &:hover {
      &::after {
        transform: unset;
        opacity: 0.1;
      }

      .blog-image {
        a {
          transform: scale(1.1) rotate(4deg);
        }
      }
    }

    .blog-image {
      position: relative;
      overflow: hidden;
      margin-bottom: 15px;
      border-radius: 0;
      height: 244px;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }

      a {
        transition: all 0.3s ease-in-out;
      }

      label {
        @include flex_common;
        position: absolute;
        top: 0;
        bottom: unset;
        left: 0;
        border-radius: 0;
        background-color: #febc5c;
        color: #ffffff;
        padding: 10px 13px;
        text-transform: uppercase;
        font-size: 13px;
        font-weight: 600;
        display: none;

        i {
          @include pseudowh($width: 16px, $height: 16px);
          object-fit: contain;
          margin-right: 3px;
          color: #ffffff;
        }
      }
    }

    .blog-contain {
      padding: 0 20px 20px;

      &-2 {
        padding: 0;
      }

      .blog-label {
        @include flex_wrap($dis: flex, $wrap: wrap, $gap: 2px 15px);
        align-items: center;

        .time,
        .super {
          display: flex;
          align-items: center;
          color: $content-color;
          text-transform: uppercase;
          font-size: 13px;
          gap: 5px;

          i {
            font-size: 16px;
          }

          .feather {
            @include pseudowh($width: 16px, $height: 16px);
            object-fit: contain;
            margin-right: 4px;

            body.rtl & {
              margin-right: unset;
              margin-left: 4px;
            }
          }

          span {
            margin-top: 1px;
          }
        }
      }

      a {
        color: $title-color;

        &:hover {
          color: $title-color;
        }

        h3 {
          margin: 4px 0 6px;
          font-weight: 600;
          line-height: 1.5;
          text-transform: capitalize;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;
          font-size: calc(16px + (18 - 16) * ((100vw - 320px) / (1920 - 320)));
          transition: all 0.3s ease-in-out;
        }
      }

      p {
        line-height: 1.8;
        color: $content-color;
        margin-bottom: 0;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;

        @include mq-max(2xl) {
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;
        }
      }

      .blog-button {
        border: none;
        font-size: 16px;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 0;
        overflow: hidden;
        border-radius: 5px;
        padding: 10px 22px;
        color: var(--theme-color);
        transition: all 0.3s ease-in-out;
        margin-top: calc(8px + (22 - 8) * ((100vw - 320px) / (1920 - 320)));
        font-size: 14px;
        width: max-content;

        &:after {
          @include pos;
          background-color: var(--theme-color);
          opacity: 0.1;
          left: 0;
          top: 0;
          @include pseudowh;
          z-index: -1;
        }

        &:hover {
          color: #ffffff;

          &:after {
            opacity: 1;
          }
        }

        i {
          margin-left: 10px;
          font-size: 20px;
          line-height: 1;

          body.rtl & {
            margin-left: unset;
            margin-right: 10px;
          }
        }
      }
    }

    &.sticky-blog-box {
      overflow: hidden;
      position: relative;
      z-index: 0;
      border-color: rgb(26, 164, 136, 0.28);

      &::before {
        @include pos($pos: absolute, $content: "\f038");
        @include remixicon;
        @include flex_common;
        @include pseudowh($width: 33px, $height: 33px);
        color: #ffffff;
        top: 8px;
        right: 8px;
        background-color: var(--theme-color);
        z-index: 1;
        border-radius: 6px;
        font-size: 18px;

        body.rtl & {
          left: 8px;
          right: unset;
          -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
          -webkit-filter: FlipH;
          filter: FlipH;
        }
      }

      &::after {
        content: none;
      }
    }

    .blog-label-tag {
      position: absolute;
      top: 15px;
      left: 0;
      padding: 5px 13px;
      background-color: #ffba00;
      font-size: 14px;
      color: #ffffff;
      font-weight: 500;
      z-index: 1;

      &::before {
        @include pos;
        @include pseudowh($width: 15px, $height: 15px);
        top: 0;
        right: -15px;
        border-bottom: solid 15px #ffba00;
        border-left: solid 15px transparent;
        border-right: solid 15px transparent;
        transform: rotate(180deg);
        z-index: 1;
      }

      &::after {
        @include pos;
        @include pseudowh($width: 15px, $height: 15px);
        top: 16px;
        right: -15px;
        border-bottom: solid 15px #ffba00;
        border-left: solid 15px transparent;
        border-right: solid 15px transparent;
        transform: rotate(0);
        z-index: 1;
      }

      span {
        font-weight: 700;
      }
    }
  }

  .blog-list {
    display: flex;
    align-items: center;
    transition: all 0.3s ease-in-out;

    @include mq-max(xl) {
      display: block;
    }

    @include mq-max(lg) {
      display: flex;
    }

    @include mq-max(md) {
      display: block;
    }

    &:hover {
      box-shadow: 0 4px 9px rgba($title-color, 0.1);

      .blog-image {
        img {
          transform: scale(1.1);
        }
      }

      .blog-contain {
        a {
          h3 {
            color: var(--theme-color);
          }
        }

        .blog-button {
          background-color: var(--theme-color);
          color: #ffffff;
        }
      }
    }

    .blog-image {
      margin-bottom: -1px;
      width: 38%;
      transition: all 0.3s ease-in-out;
      height: calc(257px + (300 - 257) * ((100vw - 320px) / (1920 - 320)));

      @media (max-width: 1399px) {
        width: 45%;
      }

      @include mq-max(xl) {
        width: 100%;
      }

      @include mq-max(lg) {
        width: 45%;
      }

      @include mq-max(md) {
        @include pseudowh($width: 100%, $height: auto);
        margin-bottom: 8px;
        text-align: center;
      }

      img {
        transition: all 0.3s ease-in-out;
        width: 100%;
        height: 100%;
        object-fit: cover;

        @include mq-max(lg) {
          width: auto;
        }

        @include mq-max(md) {
          width: 100%;
        }
      }
    }

    .blog-contain {
      padding: calc(10px + (25 - 10) * ((100vw - 320px) / (1920 - 320)));
      width: 62%;

      @media (max-width: 1399px) {
        width: 55%;
      }

      @include mq-max(xl) {
        width: 100%;
      }

      @include mq-max(lg) {
        width: 55%;
      }

      @include mq-max(md) {
        width: 100%;
      }

      p {
        line-height: 1.8;
        color: $content-color;
        margin-bottom: 0;
        -webkit-line-clamp: 4;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;

        @include mq-max(2xl) {
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          display: -webkit-box;
          overflow: hidden;
        }
      }
    }
  }

  .blog-detail-image {
    position: relative;

    .blog-image-contain {
      position: absolute;
      bottom: 0;
      width: 100%;
      padding: 55px 15px 40px;
      text-align: center;
      background: $white;
