          +li {
            margin-left: 30px;

            body.rtl & {
              margin-left: unset;
              margin-right: 30px;
            }

            &::before {
              @include pos;
              @include pseudowh($width: 15px, $height: 1px);
              @include center(vertical);
              background-color: $title-color;
              left: -24px;

              body.rtl & {
                right: -24px;
                left: unset;
              }
