      position: relative;
      color: $white;

      h3 {
        font-size: calc(18px + (28 - 18) * ((100vw - 320px) / (1920 - 320)));
        line-height: calc(24px + (35 - 24) * ((100vw - 320px) / (1920 - 320)));
        margin-bottom: calc(8px + (20 - 8) * ((100vw - 320px) / (1920 - 320)));
      }

