        @include pos($pos: absolute, $content: "\f10d");
        @include font;
        font-size: calc(38px + (70 - 38) * ((100vw - 320px) / (1920 - 320)));
        color: var(--theme-color);
        top: calc(-6px + (26 - -6) * ((100vw - 320px) / (1920 - 320)));
        left: calc(15px + (87 - 15) * ((100vw - 320px) / (1920 - 320)));
