  }

  .leave-title {
    margin: 40px 0 9px;

    h3 {
      position: relative;
      font-weight: 700;
      z-index: 0;
      color: $content-color;
      font-size: 22px;
    }
  }

  .user-comment-box {
    margin-top: 24px;

    ul {
      display: flex;
      flex-wrap: wrap;

      body.rtl & {
        padding-left: unset;
        padding-right: 0;
      }

      li {
        padding: 0px 0 50px;
        width: 100%;
        position: relative;

        &.li-padding {
          padding-left: calc(28px + (80 - 28) * ((100vw - 320px) / (1920 - 320)));
        }

        @include mq-max(xs) {
          padding: 0;
        }

        .user-box {
          display: flex;
          align-items: flex-start;

          @include mq-max(xs) {
            display: block;
          }

          .reply-button {
            position: absolute;
            top: 0;
            right: 0;
            display: flex;
            align-items: center;

            body.rtl & {
              right: unset;
              left: 0;
            }

            i {
              margin: -3px 9px 0 0;
              color: #777;

              body.rtl & {
                margin: -3px 0 0 9px;
              }
            }
          }

          .user-iamge {
            position: relative;

            img {
              @include pseudowh($width: 60px, $height: 60px);
              object-fit: cover;
              border-radius: 100%;
              overflow: hidden;
            }

            .user-name {
              position: absolute;
              top: 0;
              left: 80px;
              width: 220px;

              body.rtl & {
                right: 80px;
                left: unset;
              }

              @include mq-max(xs) {
                @include center(vertical);
              }

              h5 {
                font-weight: 600;
              }

              h6 {
                color: $content-color;
                margin-bottom: 6px;
              }
            }
          }

          .user-contain {
            width: calc(100% - 70px);
            padding-left: 20px;

            body.rtl & {
              padding-left: unset;
              padding-right: 20px;
            }

            @include mq-max(xs) {
              width: 100%;
              padding: 0;
              margin-top: 14px;
            }

            p {
              font-size: calc(13px + (15 - 13) * ((100vw - 320px) / (1920 - 320)));
              line-height: 1.6;
              color: $content-color;
              margin-bottom: 0;
              margin-top: 50px;
              width: 85%;

              @include mq-max(sm) {
                width: 100%;
              }

              @include mq-max(xs) {
                margin: 0 0 35px;
              }
            }
          }
        }
      }
    }
  }

  .leave-box {
    padding: calc(18px + (30 - 18) * ((100vw - 320px) / (1920 - 320)));
    background-color: $light-gray;
    border-radius: 6px;

    .leave-comment {
      .comment-notes {
        p {
          line-height: 1.6;
        }
      }

      .save-comment-box {
        margin: 20px 0 0;
        display: flex;
        align-items: center;

        .form-check {
          display: flex;
          align-items: center;

          input {
            margin-top: -5px;

            @media (max-width: 659px) {
              margin-top: -21px;
            }

            @media (max-width: 341px) {
              margin-top: -44px;
            }
          }

          .form-check-label {
            font-size: 15px;
            font-weight: 400;
            color: $content-color;
          }
        }
      }

      .blog-input {
        .form-control {
          border: none;

          &:focus {
            background-color: $white;
          }
        }
      }
    }
  }
}

