    border-radius: 8px;
    height: 100%;

    .order-tracking-icon {
      .feather {
        @include pseudowh($width: 25px, $height: auto);
        stroke-width: 1.4px;
      }
    }

    .order-details-name {
      h2 {
        font-size: calc(20px + (24 - 20) * ((100vw - 320px) / (1920 - 320)));
      }

      h4 {
        line-height: 1.4;
        font-weight: 500;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
      }

      h5 {
        margin-bottom: 7px;
      }

      img {
        margin-top: calc(6px + (12 - 6) * ((100vw - 320px) / (1920 - 320)));
        width: calc(160px + (200 - 160) * ((100vw - 320px) / (1920 - 320)));
      }
    }
  }

  .progtrckr {
    margin: 15px 0 0;
    padding: 0;
    display: flex;
    align-items: flex-start;

    @include mq-max(sm) {
      display: inline;
      margin: 30px 0;
      overflow: hidden;
    }

    li {
      display: inline-block;
      text-align: center;
      margin: 10px 0;
      position: relative;

      @include mq-max(sm) {
        margin: 0;
        width: 50% !important;
      }

      h5 {
        margin-top: 20px;
        text-align: center;
        width: 100%;
        display: flex;
        justify-content: center;
        font-weight: 500;

        @include mq-max(sm) {
          display: block;
          text-align: left;
          margin-top: 0;

          body.rtl & {
            text-align: right;
          }
        }
      }

      &:before {
        position: relative;
        top: -2px;
        float: left;
        left: 50% !important;
        line-height: 1;
        transform: translate(-50%, -50%) !important;

        @include mq-max(sm) {
          position: relative;
          top: 10px;
          float: left;
          left: -2px !important;
          line-height: 1;
          transform: translate(-50%, -50%) !important;

          body.rtl & {
            float: right;
            right: -32px !important;
            left: unset !important;
          }
        }
      }

      &.progtrckr-todo {
        color: $content-color;
        border-top: 4px solid #959595;
        width: 100% !important;

        @include mq-max(sm) {
          position: relative;
          text-align: left;
          margin-left: 30px;
          align-items: flex-start;
          padding-bottom: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
          padding-left: 30px;
          border-top: unset;
          border-left: 4px solid #959595;

          body.rtl & {
            border-left: none;
            border-right: 4px solid $content-color;
            margin-left: 0;
            margin-right: 30px;
            align-items: flex-end;
            padding-left: 0;
            padding-right: 30px;
            text-align: right;
          }
        }

        &:before {
          @include font;
          @include flex_common;
          @include pseudowh($width: 30px, $height: 30px);
          padding-top: 2px;
          content: "";
          color: $white;
          background: #959595;
          line-height: 35px;
          border: none;
          border-radius: 35px;
          font-size: 16px;

          @include mq-max(sm) {
            position: absolute;
            top: 10px;
            left: -17px;
            line-height: 1;
            transform: translateY(-50%);

            body.rtl & {
              left: unset;
              right: -17px;
            }
          }
        }

        h6 {
          font-size: 13px;
          margin-top: 8px;
        }
      }

      &.progtrckr-done {
        color: $title-color;
        border-top: 4px solid var(--theme-color);
        width: 100% !important;

        &:first-of-type {
          @include mq-max(sm) {
            padding-top: 5px;
          }
        }

        @include mq-max(sm) {
          position: relative;
          text-align: left;
          margin-left: 30px;
          align-items: self-start;
          padding-bottom: calc(20px + (40 - 20) * ((100vw - 320px) / (1920 - 320)));
          border-top: unset;
          border-left: 4px solid var(--theme-color);

          body.rtl & {
            margin-left: 0;
            margin-right: 30px;
            text-align: right;
            border-left: none;
            border-right: 4px solid var(--theme-color);
            align-items: flex-end;
          }
        }

        &:before {
          @include font;
          @include flex_common;
          @include pseudowh($width: 30px, $height: 30px);
          padding-top: 2px;
          content: "\f00c";
          color: #ffffff;
          background-color: var(--theme-color);
          line-height: 35px;
          border: none;
          border-radius: 35px;
          font-size: 16px;
        }

        h6 {
          font-size: 13px;
          margin-top: 8px;
          color: $content-color;
        }
      }
    }
  }
}

