        box-shadow: 0 0 8px rgba($title-color, 0.07);

        h1 {
          color: $content-color;
          line-height: 1;
          font-size: calc(43px + (70 - 43) * ((100vw - 320px) / (1920 - 320)));
        }
      }
    }
  }
}

.seller-grid-box-1 {
  .grid-image {
    @include flex_wrap($dis: flex, $wrap: nowrap, $gap: 18px);
    align-items: center;
    margin-bottom: 20px;

    .image {
      @include pseudowh($width: 66px, $height: 66px);
      border-radius: 11px;
    }

    .contain-name {
      width: calc(100% - 66px - 18px);
      position: relative;

      .since-number {
        display: flex;
        align-items: center;
        position: absolute;
        top: 0;
        background-color: $light-gray;
        right: 0;
        padding-left: 5px;

        body.rtl & {
          right: unset;
          left: 0;
        }

        @include mq-max(xs) {
          display: block;
        }

        h6 {
          color: $content-color;
        }

        .product-rating {
          display: block;
          text-align: right;

          body.rtl & {
            text-align: left;
          }

          @include mq-max(xs) {
            margin-top: 2px;
          }
        }
      }

      h3 {
        font-size: calc(19px + (23 - 19) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 600;
        margin-bottom: 10px;
        color: $title-color;
        width: 50%;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        display: -webkit-box;
        overflow: hidden;
      }

      .product-label {
        padding: 5px 10px;
        font-size: 13px;
        letter-spacing: 0.5px;
        border-radius: 4px;
        color: var(--theme-color);
        position: relative;
        z-index: 0;

        &:after {
          @include pos($pos: absolute, $content: "");
          background-color: var(--theme-color);
          opacity: 0.1;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          z-index: -1;
        }
      }
    }
  }

  .grid-contain {
    .seller-category {
      @include flex_common($dis: flex, $align: center, $justify: space-between);

      .product-image {
        display: flex;
        flex-wrap: nowrap;

        li {
          @include pseudowh($width: calc(35px + (40 - 35) * ((100vw - 320px) / (1920 - 320))),
            $height: calc(35px + (40 - 35) * ((100vw - 320px) / (1920 - 320))));
          @include flex_common;
          background: $white;
          border-radius: 100%;
          padding: 4px;
          box-shadow: 0 0 2px rgba($title-color, 0.2);
          color: $content-color;
          transition: all 0.3s ease-in-out;

          &:hover {
            transform: scale(1.1) translateY(-7px);
          }

          &:last-child {
            line-height: 1;
            font-weight: 600;
            font-size: calc(13px + (14 - 13) * ((100vw - 320px) / (1920 - 320)));
          }

          +li {
            margin-left: calc(-19px + (-14 - -19) * ((100vw - 320px) / (1920 - 320)));

            body.rtl & {
              margin-right: calc(-19px + (-14 - -19) * ((100vw - 320px) / (1920 - 320)));
              margin-left: unset;
            }
          }
        }
      }
    }
  }
}

/**=====================
     Seller Dashboard Scss
==========================**/
.product-table {
  margin-bottom: 0;

  thead {
    tr {
      th {
        text-align: center;
        font-size: 15px;
        font-weight: 600;
        white-space: nowrap;
        min-width: 90px;
        color: $title-color;
        border-color: $border-color;
      }
    }
  }

  tbody {
    border-top: 1px solid $border-color !important;

    tr {
      &:last-child {
        td {
          border-bottom: none;
          padding-bottom: 0;
        }
      }

      td {
        text-align: center;
        padding: 12px 15px;
        vertical-align: middle;
        white-space: nowrap;
        max-width: 190px;
        color: $content-color;
        font-size: 15px;

        h6 {
          font-size: 15px;
        }

        &:nth-child(2) {
          max-width: 150px;

          h6 {
            color: $content-color;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            display: -webkit-box;
            overflow: hidden;
            font-size: 15px;
          }
        }

        &.product-image {
          width: 90px;
        }

        &.efit-delete {
          .feather {
            width: 19px;
            stroke-width: 1.6px;

            &.edit {
              color: var(--theme-color);
            }

            &.delete {
              color: #ff7272;
              margin-left: 10px;

              body.rtl & {
                margin-left: unset;
                margin-right: 10px;
              }
            }
          }
        }
      }
    }
  }
}

.order-table {
  margin-bottom: 0;

  &-2 {
    tbody {
      border-top: none !important;

      tr {
        td {
          text-align: left;
        }
      }
    }
  }

  thead {
    tr {
      th {
        text-align: center;
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));
        font-weight: 600;
        white-space: nowrap;
        min-width: 90px;
      }
    }
  }

  tbody {
    border-top: 1px solid $border-color;

    tr {
      &:last-child {
        td {
          border-bottom: none;
          padding-bottom: 0;
        }
      }

      td {
        text-align: center;
        padding: 12px 15px;
        vertical-align: middle;
        white-space: nowrap;

        &:nth-child(2) {
          color: $content-color;
        }

        label {
          padding: 3px 12px;
          font-size: 12px;
          border-radius: 50px;

          &.success {
            color: var(--theme-color);
          }

          &.danger {
            background-color: rgba(255, 114, 114, 0.1);
            color: #ff7272;
          }
        }

        &:first-child {
          font-weight: 600;
        }
      }
    }
  }
}

.profile-tab {
  ul {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: calc(15px + (21 - 15) * ((100vw - 320px) / (1920 - 320)));

    li {
      display: flex;
      align-items: center;
      width: 100%;

      @include mq-max(sm) {
        display: block;
      }

      h5 {
        font-size: calc(14px + (16 - 14) * ((100vw - 320px) / (1920 - 320)));

        &:first-child {
          width: 220px;
        }

        &:last-child {
          color: $content-color;

          @include mq-max(sm) {
            margin-top: 8px;
          }
        }
      }
    }
  }
}

.deactivate-box {
  padding: 15px 22px;
  background: rgba(255, 165, 59, 0.1);
  border: 1px dashed #ffa53b;
  border-radius: 7px;
  display: flex;
  flex-wrap: nowrap;
  gap: 17px;
