}

.dashboard-privacy {
  .privacy-box {
    .custom-form-check-2 {
      label {
        font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
      }
    }
  }
}

.apexcharts-tooltip {
  * {
    display: flex;
  }
}

.apexcharts-toolbar {
  z-index: 0;
}

.product-header-top {
  .wishlist-button {
    width: 26px;
    height: 26px;
    background-color: $white;
    border-radius: 100%;
    padding: 0;
    box-shadow: 0 3px 3px rgba(34, 34, 34, 0.24);
    @include flex_common;
    line-height: 1;
    z-index: 2;
