/* price range */
.ngx-slider {
  .ngx-slider-selection {
    background: var(--theme-color) !important;
  }

  .ngx-slider-pointer {
    width: 22px !important;
    height: 22px !important;
    top: -10px !important;
    background-color: var(--theme-color) !important;

    &:after {
      top: 50% !important;
      left: 50% !important;
      transform: translate(-50%, -50%);
    }
