.no-data-added {
  text-align: center;
  padding: 60px 0;
  border-radius: 12px;
  background-color: rgba(var(--bs-light-rgb), var(--bs-bg-opacity));

  html.dark & {
    background-color: #1d1d22 !important;
  }

  img {
    width: 25%;
    margin-bottom: 20px;
  }

  h4 {
    font-weight: 500;
    font-size: calc(20px + (25 - 20) * ((100vw - 320px) / (1920 - 320)));
    color: #232323;
    margin-bottom: 0;

    html.dark & {
      color: #ffffff;
    }
  }

  &.collection-no-data {
    padding: 120px 0;

    img {
      width: 30%;

      @include mq-max(2xl) {
        width: 42%;
      }

      @include mq-max(md) {
        width: 60%;
      }

      @include mq-max(xs) {
        width: 80%;
      }
    }

    p {
      width: 44%;

      @media (max-width: 1206px) {
        width: 53%;
      }

      @media (max-width: 1100px) {
        width: 64%;
      }

      @include mq-max(sm) {
        width: 80%;
      }

      @include mq-max(xs) {
        width: 100%;
      }
    }
  }

  p {
    width: 100%;
    margin: 10px auto 0;
    font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
    line-height: 1.4;
    color: $content-color;
  }

  &.category-no-data {
    img {
      width: 64%;
    }
  }

  &.no-shipping {
    img {
      width: 32%;

      @media (max-width: 991px) {
        width: 42%;
      }

      @media (max-width: 576px) {
        width: 50%;
      }

      @media (max-width: 420px) {
        width: 80%;
      }
    }
  }
}

.post-question-box {
  margin-bottom: 32px;

  h4 {
    color: $title-color;
    font-weight: 500;
    font-size: calc(15px + (18 - 15) * ((100vw - 320px) / (1920 - 320)));
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 8px;

    a {
      width: auto;
      display: inline-block;
      font-size: calc(14px + (17 - 14) * ((100vw - 320px) / (1920 - 320)));
      font-weight: 500;
      border: none;
      border-radius: 6px;
      background-color: #f8f8f8;
      color: #222;
      padding: calc(9px + (12 - 9) * ((100vw - 320px) / (1920 - 320))) calc(16px + (22 - 16) * ((100vw - 320px) / (1920 - 320)));

      html.dark & {
        background-color: #1d1d22;
        color: #ffffff;
      }
    }
  }
}

.wishlist-section {
  >.container-fluid-lg {
    .full_border {
      margin: 0;

      >div {
        margin: 0;

        @media screen and (min-width: 1399px) {
          &.col-xxl-2 {
            body.rtl & {
              border-left: 1px solid $border-color;
              border-right: unset;

              html.dark & {
                border-color: rgba(119, 119, 119, 0.44);
              }
            }

            &:nth-child(6n-6) {
              body.rtl & {
                border-left: unset;
                border-right: unset;
              }
            }
          }
        }

        @media (max-width: 1401px) and (min-width: 991px) {
          &.col-lg-3 {
            border-right: 1px solid $border-color;
            border-left: unset;

            body.rtl & {
              border-left: 1px solid $border-color;
              border-right: unset;

              html.dark & {
                border-color: rgba(119, 119, 119, 0.44);
              }
            }

            &:nth-child(6n-6) {
              body.rtl & {
                border-left: 1px solid $border-color;
                border-right: unset;
              }
            }

            &:nth-child(4n-4) {
              body.rtl & {
                border-left: unset;
                border-right: unset;
              }
            }
          }
        }

        @media (max-width: 992px) and (min-width: 767px) {
          &.col-md-4 {
            border-right: 1px solid $border-color;
            border-left: unset;

            body.rtl & {
              border-left: 1px solid $border-color;
              border-right: unset;

              html.dark & {
                border-color: rgba(119, 119, 119, 0.44);
              }
            }

            &:nth-child(4n-4) {
              body.rtl & {
                border-left: 1px solid $border-color;
                border-right: unset;
              }
            }

            &:nth-child(3n-3) {
              body.rtl & {
                border-left: unset;
                border-right: unset;
              }
            }
          }
        }

        @media (max-width: 767px) {
          &.col-6 {
            border-right: 1px solid $border-color;
            border-left: unset;

            body.rtl & {
              border-left: 1px solid $border-color;
              border-right: unset;

              html.dark & {
                border-color: rgba(119, 119, 119, 0.44);
              }
            }

            &:nth-child(3n-3) {
              body.rtl & {
                border-left: 1px solid $border-color;
                border-right: unset;
              }
            }

            &:nth-child(2n-2) {
              body.rtl & {
                border-left: unset;
                border-right: unset;
              }
            }
          }
        }
      }
    }
  }
}