        transition: border-color 0.2s ease;

        body.rtl & {
          right: 0;
          left: unset;
          border-right: 17px solid #f9f9f6;
          border-left: unset;
        }
      }

      &.active {
        background-color: #e9f6f3;

        &:after {
          border-left: 17px solid #e9f6f3;

          body.rtl & {
            border-right: 17px solid #e9f6f3;
            border-left: unset;
          }
        }
      }

      &.cancelled-box {
        background-color: #fbe9eb;

        &:after {
          border-left: 17px solid #fbe9eb;

          body.rtl & {
            border-right: 17px solid #fbe9eb;
            border-left: unset;
          }
        }
      }

      &:first-child {
        border-radius: 6px 0 0 6px;

        body.rtl & {
          border-radius: 0 6px 6px 0;
        }

        &::before {
          display: none;
        }
      }
    }
  }

  .panel-content {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-top: 0;

    .icon {
      width: calc(30px + (45 - 30) * ((100vw - 320px) / (1920 - 320)));
    }

    .status {
      margin-top: 0;
      font-size: 17px;
      display: flex;
      flex-direction: column;
      font-weight: 600;
      white-space: nowrap;
    }
  }
}

.tracking-total {
  li {
    @include flex_common ($dis: flex, $align: center, $justify: space-between);
    width: 100%;
    font-size: calc(15px + (17 - 15) * ((100vw - 320px) / (1920 - 320)));
    margin-bottom: 6px;

    &:last-child {
      font-weight: 600;
      margin-bottom: 0;
      padding-top: 15px;
      border-top: 1px solid #e8e8e8;
      margin-top: 12px;
    }
  }
}

.download-dropdown-menu {
  padding: 8px 16px;

  html.dark & {
    background-color: #1d1d22;
  }

  body.rtl & {
    background-color: #1d1d22;
  }

  .dropdown-item {
    color: $content-color;
    padding-inline: unset;
    transition: color 0.15s ease-in-out;

    &:hover,
    &:active {
      color: $title-color;
      background-color: transparent;
    }

    +.dropdown-item {
      border-top: 1px solid #eee;

      html.dark & {
        border-color: #77777770;
      }

      body.rtl & {
        border-color: #77777770;
      }
    }
  }
}