.user-dashboard-title {
  position: relative;

  &::after {
    @include pos;
    bottom: 0;
    left: -20px;
    width: 3px;
    height: 26px;
    background-color: var(--theme-color);
    body.rtl & {
      left: unset;
      right: -20px;
    }
  }
}
